      //生成优惠价二维码 
      commodity_qc_code() {
        this.codeShow = true;
        let baseURI
        let mode = this.objMsg.mbgi_sale_mode == '3' ? '2' : this.objMsg.mbgi_sale_mode //mode 为1时商品在货道内，不为1时商品不在货道内
        let source = this.objMsg.mbgi_source == '3' ? '2' : this.objMsg.mbgi_source //source 为1时是银行商品，不为1时商品非银行商品
        let valueOf = mode + source
        console.log(valueOf, "----------------------");
        switch (valueOf) {
          case '11':
            //货道内扫码(银行自己的礼品)
            baseURI = this.customGlobal.serverUrl.postManagerGenUnionQrCodeForInnerBank;
            break;
          case '21':
            //货道外扫码(银行自己的礼品)
            baseURI = this.customGlobal.serverUrl.postManagerGenUnionQrCodeForOuter;
            break;
          case '12':
            //货道内扫码(非银行礼品)
            baseURI = this.customGlobal.serverUrl.postManagerGenUnionQrCodeForInnerMer;
            break;
          case '22':
            //货道外扫码(非银行礼品)
            baseURI = this.customGlobal.serverUrl.postManagerGenUnionQrCodeForOuterMer;
            break;
          default:
            return;
            break;
        }
        let that = this;
        this.img_commodity.msg = '';
        this.img_commodity.billNo = '';
        let _jsondata_ = {
          bank_company_alias_id: that.transcoding_id.currentCompanyId,
          totalAmount: that.objMsg.mbgi_money_2,
          sale_info: JSON.stringify({
            mbs_sale_mode: this.objMsg.mbgi_sale_mode,
            mbs_use_uuid: '',
            mbs_mer_uuid: that.objMsg.mbgi_mer_uuid,
            mbs_guest_phone: this.phone,
            mbs_goods_uuid: this.objMsg.mbgi_sale_mode == '1' ? that.objMsg.mdgi_uuid : that.objMsg.mgs_uuid,
            mbs_goods_type_uuid: that.objMsg.mbgi_uuid,
            mbs_pay_mode: '2',
            mbs_device_id: that.pri_detail.activityObj.di_uuid,
            mdgi_number: that.objMsg.mdgi_number ? that.objMsg.mdgi_number : '无'
          }),
          srcReserve: that.transcoding_id.currentCompanyId,
          mgs_device_id: that.pri_detail.activityObj.di_uuid,
          mgs_mer_uuid: that.objMsg.mbgi_mer_uuid,
          bank_center_id: that.pri_detail.activityObj.bank_center_id,
          province_id: that.pri_detail.activityObj.province_id,
          city_id: that.pri_detail.activityObj.city_id,
          county_id: that.pri_detail.activityObj.county_id,
          station_id: that.pri_detail.activityObj.station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var a1 = this.customGlobal.postHttpClient(
