          city_id: that.pri_detail.activityObj.city_id,
          county_id: that.pri_detail.activityObj.county_id,
          station_id: that.pri_detail.activityObj.station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var a1 = this.customGlobal.postHttpClient(
          baseURI,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        a1.then((res) => {
          var date = new Date();
          var year = date.getFullYear();
          var month = date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
          var day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
          var data_id = year + "-" + month + "-" + day;
          that.commodity_data_id = data_id;
          that.commodity_startDivi();
          that.commodity_startDivi1();
          if (res.data.status == 'success') {
            loading.close();
            that.img_commodity = res.data;
            console.log('二维码：' + JSON.stringify(that.img_commodity.msg));
          } else {
            console.info("生成二维码失败");
