//返回格式：1是YYYY_MM_DD_hh_mm_ss_SSS，2是YYYY-MM-DD hh:mm:ss,3是YYYYMMDDhhmmssSSS,4是YYYY-MM-DD,5是hh:mm,6是YYYYMMDD_hhmmssSSS_

const getFormatDateTimeString = type => {
  //////////
  var date = new Date();
  var year = date.getFullYear();
  var month = date.getMonth() + 1;
  var day = date.getDate();
  var hour = date.getHours();
  var minute = date.getMinutes();
  var second = date.getSeconds();
  var SSS = date.getMilliseconds();
  var dateStr = "";
  if(type=="1") {
    dateStr = year+"_"+formatNum(month)+"_"+formatNum(day)+"_"+formatNum(hour)+"_"+formatNum(minute)+"_"+formatNum(second)+"_"+SSS;
  }else if(type=="2"){
    dateStr =  year+"-"+formatNum(month)+"-"+formatNum(day)+" "+formatNum(hour)+":"+formatNum(minute)+":"+formatNum(second) ;
  }else if(type=="3"){
    dateStr = year +""+ formatNum(month)  +""+  formatNum(day)  +""+  formatNum(hour)  +""+  formatNum(minute)  +""+  formatNum(second)  +""+  SSS;
  }else if(type=="4"){
    dateStr =  year+"-"+formatNum(month)+"-"+formatNum(day)  ;
  }else if(type=="5"){
    dateStr =  formatNum(hour)+":"+formatNum(minute);
  }else if(type=="6"){
    dateStr = year +""+ formatNum(month)  +""+  formatNum(day)  +"_"+  formatNum(hour)  +""+  formatNum(minute)  +""+  formatNum(second)  +""+  formatNum2(SSS,3)+"_";
  }
  return dateStr;
  ///////////////
}
  //补位，不足两位的前面补0
const formatNum=num=>{
  if(num.toString().length<2){
    return "0"+num;
  }else{
    return num;
  }
}
const formatNum2=(num,len)=>{
  if(num.toString().length<len){

    while(num.toString().length<len){
      num = "0"+num;

    }

    return  num;
  }else{
    return num;
  }
}


