const postHttpClient2 = (_url_, _baseURL_,_headers_,_data_) => {
//export function httpClient1   (_url_, _baseURL_,_method_,_headers_,_data_)  {
  return new Promise((resolve, reject) => {

    if(_headers_==undefined || _headers_ == ''){
      _headers_ = {"Content-Type": "application/json"};
      //headers: {"Content-Type": "application/x-www-form-urlencoded"},
    }

// console.info("data:"+JSON.stringify(_data_));
    axios(
      {
        url : _url_,
        baseURL: _baseURL_,
        data: _data_,
        method:'post',
        responseType: 'blob',
        headers: _headers_,
        timeout:6000,
        withCredentials:true,//双刃剑
      }
    ).then(response => {
      console.info("commonsPost_success:"+ JSON.stringify( response.data) ) ;
      //////////////
      const blob = new Blob([response.data], { type: 'application/vnd.ms-excel;charset=utf-8' })
      let _filenames_ = response.headers['content-disposition'];
      let filename = _filenames_.match(/=(.*)$/)[1];
      if (window.navigator.msSaveOrOpenBlob) { //兼容IE10
        navigator.msSaveBlob(blob, filename);
      } else {
        //console.log(blob)
        const url = window.URL.createObjectURL(blob)
        //console.log(url)
        const aLink = document.createElement('a')
        aLink.style.display = 'none'
        aLink.href = url
        aLink.setAttribute('download', filename);
        document.body.appendChild(aLink)
        aLink.click()
        document.body.removeChild(aLink)
        window.URL.revokeObjectURL(url)
      }



      ////////////
      resolve(response);
    }, err => {
      console.info("commonsPost_err:"+ JSON.stringify( err) ) ;
      reject(err);
    })
      .catch((error) => {
        console.info("commonsPost_error:"+ JSON.stringify( error) ) ;
        reject(error)
      })
  })
}
