    parseMenu:function (e){
      //console.info("传递参数："+e);
      let _children_ = new Array();
      let _parentRoute_ = {};

      let menus = JSON.parse(e) ;

      for(let i1=0;i1< menus.length;i1++){

        //console.info("url:"+menus[i1].detail);
        for(let i2=0;i2< menus[i1].detail.length;i2++){
          // console.info("url:"+menus[i1].detail[i2].url);
          _children_.push(
            {
              name : menus[i1].detail[i2].url ,
              path : menus[i1].detail[i2].url ,

              // component : IndexRight1
              //component: resolve => require(['@/components' + menus[i1].detail[i2].vue], resolve)
              //注意全路径
               component: () => import('@/components' + menus[i1].detail[i2].vue)
            }
          );
         }
      }
      //增加额外业务路由
      _children_.push(
        {
          name:'org_query1',
          path: '/org_query1',
          component: () => import('@/components/index/right/Example4.vue' )//将组件用require引进来
        });
      _children_.push(
        {
          name:'org_query',
          path: '/org_query',
          component: () => import('@/components/views/orgInfoManager/OrgInfoCenter.vue' )//将组件用require引进来
        });
      _children_.push(
        {
          name:'org_query_bank',
          path: '/org_query_bank',
