    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },

    loginSubmit:function(e){
      //console.info("username:"+this.username+",passwd:"+this.customGlobal.md5(this.password));


      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });

      //alert($("#d1").text());
      //this.customGlobal.userInfo =  {username:"hw",authed:"success"};
      //console.info("lastUrl:"+JSON.stringify(this.customGlobal.lastUrl));
      //this.$router.push({name:this.global.lastUrl});

      // this.$router.push({name:'mainView'});
      //this.$router.push({path:'/index'});

      // this.axios({
      //   url:'/fr/postMerCheckLogin',
      //   baseURL:'http://localhost:28080',
      //   // headers: {"Content-Type": "application/json"},
      //   //headers: {"Content-Type": "application/x-www-form-urlencoded"},
      //   //method:'get',
      //   method:'post',
      //   data: {
      //   //params: {
      //     username: 'sa',
      //     password:'123456'
      //   },
      //   timeout:6000
      // })
      //   .then(function (response) {
      //     console.log(response);
      //     console.log("服务器返回业务数据："+JSON.stringify(response.data));
      //   },function (err){
      //     console.log("error:"+err);
      //   })
      //   .catch(function (error) {
      //     console.log("出错："+error);
      //   });

     var p1 = this.customGlobal.postHttpClient(
       this.customGlobal.serverUrl.postMerCheckLogin2Bank,
        // this.customGlobal.serverUrl.baseURI,
       this.customGlobal.serverUrl.orgBaseURI,
       {"Content-Type": "application/json"},
      // {username:this.username,password:this.password});
       {username:this.username,password:this.customGlobal.md5(this.password)});


     p1.then((res) => {
        console.info("加密："+this.customGlobal.encrypt("http://www.baidu.com",this.customGlobal.cryptoKey));

        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.authstatus=='success'){
          let heat_permission = [];
          let heat_buttonn = [];
          let platfrom_permisson = [];
          let platfrom_button = [];
          let menu = JSON.parse(res.data.menu);
          if(menu.hasOwnProperty("heatconfig")){//热力账号
            if(menu.heatconfig.hasOwnProperty("permissionconfig")){
                heat_permission = menu.heatconfig.permissionconfig.detail;
            }else if(menu.heatconfig.hasOwnProperty("buttonconfig")){
                heat_buttonn = menu.heatconfig.buttonconfig.detail;
            }

          }
          if(menu.hasOwnProperty("platfromconfig")){//平台账号
            if(menu.platfromconfig.hasOwnProperty("permissionconfig")){
                platfrom_permisson = menu.platfromconfig.permissionconfig.detail;
            }else if(menu.platfromconfig.hasOwnProperty("buttonconfig")){
                platfrom_button = menu.platfromconfig.buttonconfig.detail;
            }

          }

          platfrom_permisson = platfrom_permisson.concat(heat_permission);
          platfrom_button = platfrom_button.concat(heat_buttonn);
          console.info("platfrom_permisson:"+JSON.stringify( platfrom_permisson) );
          //console.info("heat_permission:"+JSON.stringify( heat_permission) );
          //菜单
          this.customGlobal.userMenuForPermission = platfrom_permisson;
          this.customGlobal.userMenuForButton = platfrom_button;

          this.parseMenu( JSON.stringify(platfrom_permisson) );
          this.customGlobal.userInfo =  {username:res.data.owner_uuid,authed:"success"};


          //用户信息
          this.customGlobal.userInfoExt =  JSON.parse(res.data.userInfo);
          this.customGlobal.userMenu = JSON.parse(res.data.menu);
          this.customGlobal.systemDictionary = JSON.parse(res.data.dic);
          //权限点
          this.customGlobal.permissionKeyOrganKey = {permissionkey:JSON.parse(res.data.permissionkey),orgkey:JSON.parse(res.data.orgkey),btnkey:JSON.parse(res.data.btnkey)};
          // console.info("this.customGlobal.permissionKeyOrganKey:"+JSON.stringify(this.customGlobal.permissionKeyOrganKey));
          //机构模板
          this.customGlobal.userOrgan = JSON.parse(res.data.organ);
          //console.info("organ:"+JSON.stringify(this.customGlobal.userOrgan));
          loading.close();

          // window.sessionStorage.setItem('serverUrl',JSON.stringify(this.customGlobal.serverUrl));
          //window.sessionStorage.setItem('global',JSON.stringify(this.customGlobal));
          window.sessionStorage.setItem('userInfo',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.userInfo),this.customGlobal.cryptoKey) );
          window.sessionStorage.setItem('userMenu',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.userMenu),this.customGlobal.cryptoKey) );
          window.sessionStorage.setItem('userInfoExt',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.userInfoExt),this.customGlobal.cryptoKey));
          window.sessionStorage.setItem('userMenuForPermission',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.userMenuForPermission),this.customGlobal.cryptoKey) );
          window.sessionStorage.setItem('userMenuForButton',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.userMenuForButton),this.customGlobal.cryptoKey) );
          window.sessionStorage.setItem('systemDictionary',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.systemDictionary),this.customGlobal.cryptoKey) );
          window.sessionStorage.setItem('permissionKeyOrganKey',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.permissionKeyOrganKey),this.customGlobal.cryptoKey) );
          window.sessionStorage.setItem('userOrgan',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.userOrgan),this.customGlobal.cryptoKey) );

          this.$router.push({path:'/index'});
        }else{
          console.info("认证失败");
           loading.close();


          this.$alert(res.data.msg, '认证失败', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
            loading.close();

           this.$alert(JSON.stringify(error.message), '认证出错!', {
             confirmButtonText: '确定',
             callback: action => {

             }
           });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
            loading.close();


           this.$alert(JSON.stringify(res.message), '认证出错', {
             confirmButtonText: '确定',
             callback: action => {

             }
           });
      });

      },
    wxScanCodeLogin(){
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });

      var p1 = this.customGlobal.getHttpClient(
        this.customGlobal.serverUrl.getWxMiniMerCode2Openid,
        this.customGlobal.serverUrl.orgBaseURI,
        //"https://wx.jlshskj.cn",
        {"Content-Type": "application/json"},
        // {username:this.username,password:this.password});
        {code:this.wxCode});
      p1.then((res) => {

        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.authstatus=='success'){
          let heat_permission = [];
          let heat_buttonn = [];
          let platfrom_permisson = [];
          let platfrom_button = [];
          let menu = JSON.parse(res.data.menu);
          if(menu.hasOwnProperty("heatconfig")){//热力账号
            if(menu.heatconfig.hasOwnProperty("permissionconfig")){
              heat_permission = menu.heatconfig.permissionconfig.detail;
            }else if(menu.heatconfig.hasOwnProperty("buttonconfig")){
              heat_buttonn = menu.heatconfig.buttonconfig.detail;
            }

          }
          if(menu.hasOwnProperty("platfromconfig")){//平台账号
            if(menu.platfromconfig.hasOwnProperty("permissionconfig")){
              platfrom_permisson = menu.platfromconfig.permissionconfig.detail;
            }else if(menu.platfromconfig.hasOwnProperty("buttonconfig")){
              platfrom_button = menu.platfromconfig.buttonconfig.detail;
            }

          }

          platfrom_permisson = platfrom_permisson.concat(heat_permission);
          platfrom_button = platfrom_button.concat(heat_buttonn);
          console.info("platfrom_permisson:"+JSON.stringify( platfrom_permisson) );
          //console.info("heat_permission:"+JSON.stringify( heat_permission) );
          //菜单
          this.customGlobal.userMenuForPermission = platfrom_permisson;
          this.customGlobal.userMenuForButton = platfrom_button;

          this.parseMenu( JSON.stringify(platfrom_permisson) );
          this.customGlobal.userInfo =  {username:res.data.owner_uuid,authed:"success"};
          //用户信息
          this.customGlobal.userInfoExt =  JSON.parse(res.data.userInfo);
          this.customGlobal.userMenu = JSON.parse(res.data.menu);
          this.customGlobal.systemDictionary = JSON.parse(res.data.dic);
          //权限点
          this.customGlobal.permissionKeyOrganKey = {permissionkey:JSON.parse(res.data.permissionkey),orgkey:JSON.parse(res.data.orgkey),btnkey:JSON.parse(res.data.btnkey)};
          // console.info("this.customGlobal.permissionKeyOrganKey:"+JSON.stringify(this.customGlobal.permissionKeyOrganKey));
          //机构模板
          this.customGlobal.userOrgan = JSON.parse(res.data.organ);
          //console.info("organ:"+JSON.stringify(this.customGlobal.userOrgan));
          loading.close();

          // window.sessionStorage.setItem('serverUrl',JSON.stringify(this.customGlobal.serverUrl));
          //window.sessionStorage.setItem('global',JSON.stringify(this.customGlobal));
          window.sessionStorage.setItem('userInfo',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.userInfo),this.customGlobal.cryptoKey) );
          window.sessionStorage.setItem('userMenu',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.userMenu),this.customGlobal.cryptoKey) );
          window.sessionStorage.setItem('userInfoExt',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.userInfoExt),this.customGlobal.cryptoKey));
          window.sessionStorage.setItem('userMenuForPermission',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.userMenuForPermission),this.customGlobal.cryptoKey) );
          window.sessionStorage.setItem('userMenuForButton',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.userMenuForButton),this.customGlobal.cryptoKey) );
          window.sessionStorage.setItem('systemDictionary',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.systemDictionary),this.customGlobal.cryptoKey) );
          window.sessionStorage.setItem('permissionKeyOrganKey',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.permissionKeyOrganKey),this.customGlobal.cryptoKey) );
          window.sessionStorage.setItem('userOrgan',this.customGlobal.encrypt(JSON.stringify(this.customGlobal.userOrgan),this.customGlobal.cryptoKey) );

          this.$router.push({path:'/index'});
        }else{
          console.info("扫码认证失败");
          loading.close();


          this.$alert(res.data.msg, '扫码认证失败', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }
      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();

        this.$alert(JSON.stringify(error.message), '扫码认证出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();


        this.$alert(JSON.stringify(res.message), '扫码认证出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    }

