.login-modal>div:nth-child(2){background: #fff;padding:45px;}
.login-modal .form{padding:0;margin:0 auto;}
.login-modal .form ul{width:100%;margin:0 auto;padding: 20px 30px;}
.login-modal .form li label{display:inline-block;width:80px;text-align: right;}
.login-modal .form li{
  height:40px;line-height: 40px;
  border-bottom:none;
  margin-top:0px;
  margin-bottom:20px;
}
.login-modal .form li input{height:40px;line-height:40px;padding-left:15px;padding-right:15px; width:200px; }
.login-logo{
  padding:0 60px;margin-top:20px;
}
.login-logo img{margin-bottom:30px;display: block;}
.login-modal .easyui-textbox + .textbox .textbox-text,
.login-modal .textbox .textbox-text{border:none;}

input::-webkit-input-placeholder {
  color:#c8c8c8;
}
input:-moz-placeholder {
  color:#c8c8c8;
}
input::-moz-placeholder {
  color:#c8c8c8;
}
input::-ms-input-placeholder {
  color:#c8c8c8;
}
.loginbtn{
  width:240px;height:40px;line-height: 40px;
  background:#5290C9;color:#fff;
  margin:0 auto;display:block;
}
.loginbtn:hover,.loginbtn:active{
  background:#4B86BC;
  color:#ffffff;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}



</style>
<style   >


.el-loading-spinner {
  top: 50%!important;
  left: 50%!important;
  margin-left: -55px!important;
  background: rgba(0, 0, 0, 0.7)!important;
  padding: 20px!important;
  border-radius: 4px!important;
  width: auto!important;
  text-align: center!important;
  position: absolute!important;



}
.el-loading-spinner> i {
  color:#FFFFFF!important;
}
.el-loading-text{
 color:#FFFFFF!important;
}

</style>



