<template>
  <div>
<!--    <a class="btn btn-primary" @click="openRoute2">{{ownerName}}</a>-->
    <router-view></router-view>
  </div>
</template>

<script>
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js'

export default {
  name: "rightContent",
  methods: {
    openRoute2:function(e){
      console.info("this.$route.params:"+JSON.stringify( this.$route.params) +",当前$route.name:"+this.$route.name );
      this.$router.push({name:'leftTree',params:{'method':'add'}});
      // this.$router.push({path:'/left' });
    }
  },
  data:function(e){
    return {
      ownerName:"第2个组件",
      ownerAlias:"组件标题"
    }
  },
  beforeRouteEnter (to, from, next) {//进入前
    console.info("com2组件内路由进入前to:"+to.name+",from:"+from);
    next();
  },
}
</script>

<style scoped>

</style>



