<template>
  <div >
<!--    <a href="#"  class="btn btn-primary" @click="callParentMethod();" title="子组件获取父组件内容">{{ msg }}</a>-->
<!--    <router-link :to="{ name: 'form1', params: { userId: 123 }}">功能一</router-link>-->
<!--    <router-link :to="{ name: 'form2', params: { userId: 123 }}">功能二</router-link>-->

    <router-link :to="{ path: '/index/views/form1', params: { userId: 123 }}">功能一</router-link>
    <router-link :to="{ path: '/index/views/form2', params: { userId: 456 }}">功能二</router-link>

  </div>
</template>

<script>
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js'

// import rightContent from "./RightContent";
export default {
  name: 'leftTree',
  //自定义属性，后期不可修改
  props: {
    pri_name: {
      type: String,
      default: '未初始化姓名'
    },
    pri_detail:{
      type:Object,
      default:function() {
          return{
            name1:'张先生',
            alias:'小张'
          }

      }
    }
  },
  methods: {
    created: function() {

      console.info('LeftTree.vue created userInfo:'+JSON.stringify(this.customGlobal.userInfo));

    },
    // components: {
    //   form1,
    //   form2
    //
    // },
    getChildDetail: function(){
      this.$data.height = Math.round(Math.random() *100)/100;
      var d1 = "pri_detail:"+JSON.stringify( this.pri_detail ) +",msg:"+this.msg +",height:" + this.$data.height +",时间："+ new Date().toLocaleTimeString();
      return d1;
    },
    callParentMethod:function(){
      this.msg = "子组件"+ Math.floor(Math.random() * 10 + 1);
      //第1个参数是父组件中声明的事件名称，第2个参数是传递给父组件参数值
      this.$emit("parent_method",[{"key1":"value1","key2":"value2"},{"key1_1":"value1_1","key2_1":"value2_1"}]);
      //this.$emit("parent_method","a","b");
      console.info("this.$route.params:"+ JSON.stringify( this.$route.params)+",当前$route.name:"+this.$route.name );
      this.$router.push({name:'rightContent',params:{'method':'list'}});
      // this.$router.push({path:'/right'});
    }
  },
  //自定义数据，后期可修改
  data:function() {
    return {
      msg: '子组件',
      height:179.08
    }

  },
  beforeRouteEnter (to, from, next) {//进入前
    console.info("leftTree组件内路由进入前to:"+to.name+",from:"+from);
    next();
  },
  beforeRouteLeave(to, from, next){//离开前
    var res=confirm("确认离开吗?");
    if(res==true){
      next();
    }else{
      next(false);
    }

  },
  beforeRouteUpdate (to, from, next) {//路由改变时
    console.info("leftTree组件内路由改变时to:"+JSON.stringify(to)+",from:"+from+",next:"+  next );
    next();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



