                  // console.log("that.data_time3:"+that.data_time3)
                  var time_id = that.data_time1 - that.data_time3;
                  console.log("time_id:" + time_id)
                  var days = Math.floor(time_id / (24 * 3600 * 1000));
                  //计算出小时数
                  var leave1 = time_id % (24 * 3600 * 1000); //计算天数后剩余的毫秒数
                  var hours = Math.floor(leave1 / (3600 * 1000));
                  //计算相差分钟数
                  var leave2 = leave1 % (3600 * 1000); //计算小时数后剩余的毫秒数
                  var minutes = Math.floor(leave2 / (60 * 1000));
                  var dayStr = days == 0 ? "" : days + "天";
                  var hoursStr = hours == 0 ? "" : hours + "时";
                  var time = dayStr + hoursStr + minutes + "分";
                  console.log("time:" + time)


