        let that = this;
        that.count_time = '';
        const TIME_COUNT = 120;
        that.count_time = TIME_COUNT;
        that.commodity_show = true;
        clearInterval(this.timer3);
        this.timer3 = null;
        that.timer3 = setInterval(() => {
          if (that.count_time > 0 && that.count_time <= TIME_COUNT) {
            that.count_time--;
          } else {
            that.commodity_show = false;
            clearInterval(that.timer3);
            that.timer3 = null;
          }
        }, 1000)

