      commodity_payment_time() {
        let that = this;
        that.payment_time = '';
        const TIME_COUNT = 30;
        that.payment_time = TIME_COUNT;
        clearInterval(that.timer5);
        that.timer5 = null;
        that.timer5 = setInterval(() => {
          if (that.payment_time > 0 && that.payment_time <= TIME_COUNT) {
            that.payment_time--;
          } else {
            that.payment_show = false;
            that.payment_show1 = false;
            this.qr_code_show = false;
            this.commodity_show = true;
            this.commodity_list_select();
