<script>
/**
 * 格式化xml字符串
 * npm install --save vkbeautify
 * import vkbeautify from 'vkbeautify'
 * vkbeautify.xml( xmlStr )
 *
 * 使用页面中引入下面内容
 * import vkbeautify from 'vkbeautify'
 *
 *
 * jtopo-0.4.8-min.js
 * 文件对键盘左、右进行了屏蔽
 * 修改源文件，删除 37==c 和 39==c 两处即可，否则el-input输入内容后，键盘左右无法控制光标左右移动。
 * 37 ——左 38 ——上 39 ——右 40 ——下
 *
 * */


//节点类型
const nodeType={
  DB_INPUT_TABLE : "DB_INPUT_TABLE",
  DB_OUTPUT_TABLE : "DB_OUTPUT_TABLE",
  XLS_READER : "XLS_READER",
  XLS_WRITER : "XLS_WRITER",
  COPY_STREAM : "COPY_STREAM",
  TRANSFORM : "TRANSFORM",
  DB_EXECUTE_TABLE : "DB_EXECUTE_TABLE",
  OUTPUT_TRASH : "OUTPUT_TRASH",
  CSV_READER : "CSV_READER",
  EXECUTE_SHELL : "EXECUTE_SHELL",
  CUSTOM_READER_WRITER : "CUSTOM_READER_WRITER",
  REDIS_WRITER : "REDIS_WRITER",
  REDIS_READER : "REDIS_READER",
  ROCKET_MQ_PRODUCER : "ROCKET_MQ_PRODUCER",
  ROCKET_MQ_CONSUMER : "ROCKET_MQ_CONSUMER",
  KAFKA_PRODUCER : "KAFKA_PRODUCER",
  KAFKA_CONSUMER : "KAFKA_CONSUMER",
}
const xmlInfo = {
  xmlHeader :"<?xml version=\"1.0\" encoding=\"UTF-8\"?>",
}




function createXmlDom() {
  let xmlDom;
  if (window.ActiveXObject) {
    xmlDom = new ActiveXObject('Microsoft.XMLDOM');
  } else if (document.implementation
    && document.implementation.createDocument) {
    xmlDom = document.implementation.createDocument('', '', null);
  } else {
    alert('您的浏览器不支持文档对象XMLDOM');
    console.info('您的浏览器不支持文档对象XMLDOM')
    return null;
  }
  // console.info("xmlDom:",xmlDom)
  return xmlDom;
}
function parseXMLToString(xmlDom) {
  if (window.ActiveXObject) {
    return xmlDom.xml;
  } else if (document.implementation
    && document.implementation.createDocument) {
    return new XMLSerializer().serializeToString(xmlDom);
  }
}
function parseStringToXML(xmlDom,str){
  if (window.ActiveXObject) {
    xmlDom.async="false";
    xmlDom.loadXML(str)
    return xmlDom;
  } else if (document.implementation
    && document.implementation.createDocument) {
    let parse = new DOMParser();
    xmlDom = parse.parseFromString(str,"text/xml");
    return xmlDom;
  }
}
function genGraph(xmlDom,graphTabPro){
  let graph = xmlDom.createElement("Graph")
  for (let k of Object.keys(graphTabPro)) {
    graph.setAttribute(k,graphTabPro[k]);
  }
  return graph;

}
/**
 *
 * */
function getNodeArrayFromXML(xmlDom,tagName){
  let nodeArray = xmlDom.getElementsByTagName(tagName)
  return nodeArray;
}
/**
 * @param xmlDom
 * @param graph 父节点
 * @param globalNodeArray 节点数组
 * @return xmlElement graph
 * */
function parseGlobalNodeArrayToXML(xmlDom,graph,globalNodeArray){
  for (let i1 = 0; i1 < globalNodeArray.length; i1++) {
    let nodePro = globalNodeArray[i1].nodePro
    let modelPro = globalNodeArray[i1].modelPro
    let node = xmlDom.createElement("Node")
    // console.info("nodePro:",nodePro)
    for (let i2 = 0; i2 <nodePro.length ; i2++) {
      // console.info("检测k:",nodePro[i2].k,"检测v:",nodePro[i2].v)
      if (nodePro[i2].k==="script" && nodePro[i2].v!=="") {
        let scriptNode = xmlDom.createElement("Script");
        scriptNode.setAttribute("name", "sqlScript");
        let newCDATA = xmlDom.createCDATASection(nodePro[i2].v);
        scriptNode.appendChild(newCDATA);
        node.appendChild(scriptNode);
        //
      } if (nodePro[i2].k==="beforeout" && nodePro[i2].v!=="") {
        let beforeOutNode = xmlDom.createElement("BeforeOut");
        let newCDATA = xmlDom.createCDATASection(nodePro[i2].v);
        beforeOutNode.appendChild(newCDATA);
        node.appendChild(beforeOutNode);
      } if (nodePro[i2].k==="afterout" && nodePro[i2].v!=="") {
        let afterOutNode = xmlDom.createElement("AfterOut");
        let newCDATA = xmlDom.createCDATASection(nodePro[i2].v);
        afterOutNode.appendChild(newCDATA);
        node.appendChild(afterOutNode);

      }else if(nodePro[i2].k==="afterout" && nodePro[i2].v==="") {
        //不做处理
      }else if(nodePro[i2].k==="beforeout" && nodePro[i2].v==="") {
        //不做处理
      }else if(nodePro[i2].k==="script" && nodePro[i2].v==="") {
        //不做处理
      }else{
        if(nodePro[i2].k!=="script" &&  nodePro[i2].k!=="beforeout" &&  nodePro[i2].k!=="afterout" ){
          // console.info("写属性：",nodePro[i2].k)
          node.setAttribute(nodePro[i2].k, nodePro[i2].v);
        }

      }

    }
    //额外处理x,y坐标
    node.setAttribute("x",modelPro.x );
    node.setAttribute("y",modelPro.y );
    node.setAttribute("isLink",modelPro.isLink );
    node.setAttribute("sortId",modelPro.sortId );
    graph.appendChild(node);
  }
  return graph;
}
/**
 * @param xmlDom
 * @param graph 父节点
 * @param globalLineArray 线数组
 * @return xmlElement graph
 * */
function parseGlobalLineArrayToXML(xmlDom,graph,globalLineArray){
  for (let i1 = 0; i1 < globalLineArray.length; i1++) {
    let lineModelPro = globalLineArray[i1].modelPro
    let lineNodePro = globalLineArray[i1].nodePro
    let line = xmlDom.createElement("Line")
    for (let k of Object.keys(lineModelPro)) {
      // console.info(k,lineModelPro[k])
      line.setAttribute(k,lineModelPro[k]);
    }
  //取metadata数据
    for (let i2 = 0; i2 < lineNodePro.length ; i2++) {
      console.info("检测k:",lineNodePro[i2]["k"],lineNodePro[i2]["v"])
      if(lineNodePro[i2]["k"]==="metadata"  ){
        console.info("line ",lineNodePro[i2]["k"],lineNodePro[i2]["v"])
        line.setAttribute(lineNodePro[i2]["k"],lineNodePro[i2]["v"]);

      }else if(lineNodePro[i2]["k"]==="order"){
        console.info("line ",lineNodePro[i2]["k"],lineNodePro[i2]["v"])
        line.setAttribute(lineNodePro[i2]["k"],lineNodePro[i2]["v"]);

      }
    }

    graph.appendChild(line);
  }
  return graph;
}
function parseGlobalMetaDataArrayToXML(xmlDom,graph,globalMetadataArray){
  for (let i1 = 0; i1 < globalMetadataArray.length; i1++) {
    let metaData = xmlDom.createElement("Metadata")
    metaData.setAttribute("id",globalMetadataArray[i1].id);
    metaData.setAttribute("sortId",globalMetadataArray[i1].sortId);
    for (let i2 = 0; i2 <globalMetadataArray[i1].fields.length ; i2++) {
      let field = xmlDom.createElement("Field")
      for (let k of Object.keys(globalMetadataArray[i1].fields[i2])) {
        field.setAttribute(k,globalMetadataArray[i1].fields[i2][k]);
      }
      metaData.appendChild(field)
    }
    graph.appendChild(metaData);
  }
  return graph;
}

function parseGlobalDbConnectArrayToXML(xmlDom,graph,globalDbConnectArray){
  console.info("parseGlobalDbConnectArrayToXML_globalDbConnectArray:",globalDbConnectArray)
  for (let i1 = 0; i1 < globalDbConnectArray.length; i1++) {

    let connection = xmlDom.createElement("Connection")
    for (let k of Object.keys(globalDbConnectArray[i1])) {
      // console.info(k,lineModelPro[k])
      connection.setAttribute(k,globalDbConnectArray[i1][k]);
    }
    graph.appendChild(connection);
  }
  return graph;
}

function parseGlobalLineCopyStreamArrayToXML(xmlDom,graph,globalLineCopyStreamArray){
   if (globalLineCopyStreamArray.length>0){
     let comment = xmlDom.createElement("Comment");
     let newCDATA = xmlDom.createCDATASection( JSON.stringify(globalLineCopyStreamArray));
     comment.appendChild(newCDATA);
     graph.appendChild(comment);
   }

  return graph;
}

export default {
name: "EtlEngineHelper",
  nodeType:nodeType,
  parseGlobalNodeArrayToXML:parseGlobalNodeArrayToXML,
  parseStringToXML:parseStringToXML,
  parseXMLToString:parseXMLToString,
  createXmlDom:createXmlDom,
  genGraph:genGraph,
  parseGlobalLineArrayToXML:parseGlobalLineArrayToXML,
  parseGlobalLineCopyStreamArrayToXML:parseGlobalLineCopyStreamArrayToXML,
  parseGlobalMetaDataArrayToXML:parseGlobalMetaDataArrayToXML,
  parseGlobalDbConnectArrayToXML:parseGlobalDbConnectArrayToXML,
  getNodeArrayFromXML:getNodeArrayFromXML,
  xmlInfo:xmlInfo,
}
</script>

<style scoped>

</style>



