  Service_fee_income_amount:'/fr/Service_fee_income_amount',//服务费收入查询
  postQueryGetDebitOrCreditNumberReport:'/fr/postQueryGetDebitOrCreditNumberReport',//查询 储蓄卡激活人次
  Number_of_participants_in_lottery:'/fr/Number_of_participants_in_lottery',//查询 抽奖活动接口

  postQueryGoodsAttachMerInfo:'/fr/postQueryGoodsAttachMerInfo',//查询 查询商品所隶属的商家
  postQuerySharingTemplate:'/fr/postQuerySharingTemplate',//查询 图片背景库
  postCreateSharingTemplate:'/fr/postCreateSharingTemplate',//新增 图片背景库图片
  postRemoveSharingTemplate:'/fr/postRemoveSharingTemplate',//删除 图片背景库图片
  postQueryActSharingTemplate:'/fr/postQueryActSharingTemplate',//查询 活动选用图片
};
export default {
  name: "serverurl",
  serverUrl :_serverUrl_
}
</script>



