    </el-dialog>
<!-- 节点属性编辑对话框end -->

  </div>
</template>

<script>

/**
 * 格式化xml字符串
 * npm install --save vkbeautify
 * import vkbeautify from 'vkbeautify'
 * vkbeautify.xml( xmlStr )
 * */
import etlTools from './EtlEngineHelper'
import vkbeautify from 'vkbeautify'




export default {
name: "EtlEngineDesigner",

  mounted:function(){
    let that = this
    that.canvasPro.currentNodeType = null
    that.canvasPro.publicScene = null
    that.canvasPro.publicStage = null
  },
  methods:{
    editNodeAtt(key){
      let that = this
      that.currentNodeManagerInfo.dialog=true
      that.currentNodeManagerInfo.attrName=key
      for (let i1 = 0; i1 < that.nodeTableData.length; i1++) {
        if(that.nodeTableData[i1].k===that.currentNodeManagerInfo.attrName){
          that.currentNodeManagerInfo.attrValue = that.nodeTableData[i1].v
          break
        }
      }
    },
    saveNodeAtt(){
      let that = this
      that.currentNodeManagerInfo.dialog=false
      that.currentNodeManagerInfo.attrValue=that.currentNodeManagerInfo.attrValue
      for (let i1 = 0; i1 < that.nodeTableData.length; i1++) {
         if(that.nodeTableData[i1].k===that.currentNodeManagerInfo.attrName){
           that.nodeTableData[i1].v=that.currentNodeManagerInfo.attrValue
           break
         }
      }
    },
    //动态增加一条metadata的field记录行
    newAddMetaDataRow(){
      if(this.metaDataManagerInfo.metadataArray.hasOwnProperty("id")){
        let rowObj =  {"name":"uuid1","type":"string","default":"","nullable":"true"}
        this.metaDataManagerInfo.metadataArray.fields.push(rowObj)
      }else{
        this.$message({
          type: 'info',
          message: '请选新增一个元数据或选择一个已存在的元数据。'
        });
      }

    },
    //创建新元数据
    createMetaData(){
      let that = this
      if (that.globalMetadataArray.length===0){
        that.globalMetadataArray.push({
          "sortId":1,
          "id":"METADATA_"+1,
          "fields":[{"name":"uuid1","type":"string","default":"","nullable":"true"}],
        })
        that.metaDataManagerInfo.selectMetadataId = "METADATA_"+1
        that.selectCurrentMetaData(this)
      }else{
        let tmp = that.sortKey2(that.globalMetadataArray,"sortId")
        let sortId = parseInt(tmp[0].sortId) + 1
        that.globalMetadataArray.push({
          "sortId":sortId,
          "id":"METADATA_"+sortId,
          "fields":[{"name":"uuid1","type":"string","default":"","nullable":"true"}],
        })
        that.metaDataManagerInfo.selectMetadataId = "METADATA_"+sortId
        that.selectCurrentMetaData(this)
      }
    },
    createDbConnection(){
      let that = this
      if (that.globalDbConnectArray.length===0){
        that.globalDbConnectArray.push({
          "sortId":1,
          "id":"CONNECT_"+1,
          "type":"MYSQL",
          "dbURL":"",
          "database":"",
          "username":"",
          "password":"",
          "token":"",
          "org":"",
        })
        that.dbConnectionManagerInfo.selectDbConnectionId = "CONNECT_"+1
        that.selectCurrentDbConnection(this)
      }else{
        let tmp = that.sortKey2(that.globalDbConnectArray,"sortId")
        let sortId = parseInt(tmp[0].sortId) + 1
        that.globalDbConnectArray.push({
          "sortId":sortId,
          "id":"CONNECT_"+sortId,
          "type":"MYSQL",
          "dbURL":"",
          "database":"",
          "username":"",
          "password":"",
          "token":"",
          "org":"",
        })
        that.dbConnectionManagerInfo.selectDbConnectionId = "CONNECT_"+sortId
        that.selectCurrentDbConnection(this)
      }
    },


    //删除元数据
    removeMetaData(){
      let that = this
      if(that.metaDataManagerInfo.selectMetadataId===''){
        this.$message({
          type: 'info',
          message: '请选选择一个已存在的元数据。'
        });
      }else{
        for (let i1 = 0; i1 < that.globalMetadataArray.length; i1++) {
          if(that.metaDataManagerInfo.selectMetadataId===that.globalMetadataArray[i1].id){
            that.globalMetadataArray.splice(i1,1)
            this.$message({
              type: 'success',
              message: '删除['+that.metaDataManagerInfo.selectMetadataId+']成功!'
            });
            that.metaDataManagerInfo.selectMetadataId=''
            that.metaDataManagerInfo.metadataArray.fields=null
            break
          }
        }
      }
    },
    removeDbConnection(){
      let that = this
      if(that.dbConnectionManagerInfo.selectDbConnectionId===''){
        this.$message({
          type: 'info',
          message: '请选选择一个已存在的数据源。'
        });
      }else{
        for (let i1 = 0; i1 < that.globalDbConnectArray.length; i1++) {
          if(that.dbConnectionManagerInfo.selectDbConnectionId===that.globalDbConnectArray[i1].id){
            that.globalDbConnectArray.splice(i1,1)
            this.$message({
              type: 'success',
              message: '删除['+that.dbConnectionManagerInfo.selectDbConnectionId+']成功!'
            });
            that.dbConnectionManagerInfo.selectDbConnectionId=''
            that.dbConnectionManagerInfo.dbConnectionArray=null
            break
          }
        }
      }
    },

    saveMetaData(){
      let that = this
      let find =false
      if(that.globalMetadataArray.length>0){
        for (let i1 = 0; i1 < that.globalMetadataArray.length; i1++) {
            let fields = that.globalMetadataArray[i1].fields
            for (let i2 = 0; i2 < fields.length; i2++) {
              let rowKey = Object.keys( fields[i2]).length
              if (rowKey!=4){//保证要有4个key
                this.$message({
                  type: 'info',
                  message: '['+that.globalMetadataArray[i1].id +']有空数据，不能保存'
                });
                find = true
                break
              }else{
                if (fields[i2].name==="" ||fields[i2].type===""||fields[i2].nullable==="" ){
                  this.$message({
                    type: 'info',
                    message: '['+that.globalMetadataArray[i1].id +']中[name或type或nullable]不可为空，不能保存'
                  });
                  find = true
                  break
                }
              }
            }
            if (find===true){
              break
            }
        }
        if (find===false){
           that.metaDataManagerInfo.metaDataDialog = false
          console.info("that.globalMetadataArray:",that.globalMetadataArray)
        }

      }else{
        that.metaDataManagerInfo.metaDataDialog = false
      }

    },
    saveDbConnectionData(){
      let that = this
      let find =false
      if(that.globalDbConnectArray.length>0){
        for (let i1 = 0; i1 < that.globalDbConnectArray.length; i1++) {
           if(that.globalDbConnectArray[i1].database===""){
             this.$message({
               type: 'info',
               message: '['+that.globalDbConnectArray[i1].id +']中[database]不可为空，不能保存'
             });
             find=true
             break
           }

        }
        if (find===false){
           that.dbConnectionManagerInfo.dbConnectionDialog  = false
          console.info("that.globalDbConnectArray:",that.globalDbConnectArray)
        }

      }else{
        that.dbConnectionManagerInfo.dbConnectionDialog = false
      }

    },


    handleEditRow(index, row) {
      console.log("行号："+index+",行内容：" + JSON.stringify(row) );
      let rowObj = {}
      this.metaDataManagerInfo.metadataArray.fields.push(rowObj)
    },
    deleteMetaDataFieldRow(index, row) {
      let that = this
      console.log("行号："+index+",行内容：" + JSON.stringify(row) );
      this.$confirm('请再次确认是否要删除[<span style="color:red;">'+row.name+'</span>]该行记录?', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString:true,
        closeOnClickModal:false,
        closeOnPressEscape:false,

      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
        console.info(that.metaDataManagerInfo.metadataArray.fields)
        for (let i1 = 0; i1 < that.metaDataManagerInfo.metadataArray.fields.length; i1++) {
          console.info("行key总数：",Object.keys(that.metaDataManagerInfo.metadataArray.fields[i1]).length)
        }
        this.metaDataManagerInfo.metadataArray.fields.splice(index,1)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    selectCurrentMetaData(event){
      console.info(this.metaDataManagerInfo.selectMetadataId,event)
      for (let i1 = 0; i1 < this.globalMetadataArray.length; i1++) {
        console.info("id:",this.globalMetadataArray[i1].id)
        if(this.metaDataManagerInfo.selectMetadataId===this.globalMetadataArray[i1].id){
          this.metaDataManagerInfo.metadataArray = this.globalMetadataArray[i1]

          break
        }
      }
    },
    selectCurrentDbConnection(event){
      console.info(this.dbConnectionManagerInfo.selectDbConnectionId,event)
      for (let i1 = 0; i1 < this.globalDbConnectArray.length; i1++) {
        console.info("id:",this.globalDbConnectArray[i1].id)
        if(this.dbConnectionManagerInfo.selectDbConnectionId===this.globalDbConnectArray[i1].id){
          this.dbConnectionManagerInfo.dbConnectionArray = [this.globalDbConnectArray[i1]]//封成数组

          break
        }
      }
    },


    cilckTab(tab, event) {
      // console.info("this.tab  :",tab ,"event:",event)
      if(tab.index=="1"){
        // let xmlString = this.parseXMLToString(this.createXMLDOM())
        // let xmlString = this.createXMLExample(thinpms.createXMLDOMExample())
        let xmlDom = etlTools.createXmlDom();
        let graph = etlTools.genGraph(xmlDom,this.graphTabPro)
        graph = etlTools.parseGlobalNodeArrayToXML(xmlDom,graph,this.globalNodeArray)
        graph = etlTools.parseGlobalLineArrayToXML(xmlDom,graph,this.globalLineArray)
        graph = etlTools.parseGlobalMetaDataArrayToXML(xmlDom,graph,this.globalMetadataArray)
        graph = etlTools.parseGlobalDbConnectArrayToXML(xmlDom,graph,this.globalDbConnectArray)
        graph = etlTools.parseGlobalLineCopyStreamArrayToXML(xmlDom,graph,this.globalLineCopyStreamArray)

        let result = vkbeautify.xml( etlTools.parseXMLToString(graph) );
        this.graphXml = etlTools.xmlInfo.xmlHeader + "\r\n"+ result;
        console.info("xml:",result)
      }
    },
    createXMLDOMExample() {
      let xmlDOM;
      if (window.ActiveXObject) {
        xmlDOM = new ActiveXObject('Microsoft.XMLDOM');
      } else if (document.implementation
        && document.implementation.createDocument) {
        xmlDOM = document.implementation.createDocument('', '', null);
      } else {
        alert('您的浏览器不支持文档对象XMLDOM');
        return;
      }
      // console.info("xmlDOM:",xmlDOM)
      return xmlDOM;
    },
    parseXMLToString(xmlDOM) {
      if (window.ActiveXObject) {
        return xmlDOM.xml;
      } else if (document.implementation
        && document.implementation.createDocument) {
        return new XMLSerializer().serializeToString(xmlDOM);
      }
    },
    parseStringToXML(xmlDOM,str){
      if (window.ActiveXObject) {
        xmlDOM.async="false";
        xmlDOM.loadXML(str)
        return xmlDOM;
      } else if (document.implementation
        && document.implementation.createDocument) {
        let parse = new DOMParser();
        xmlDOM = parse.parseFromString(str,"text/xml");
        return xmlDOM;
      }
    },
    /**
     * 创建XML 文件例子:
     * <graph>
     *     <set name='1' value='1'></set>
     *     <set name='2' value='2'></set>
     * </graph>
     *
     *
     *
     */


    createXMLExample(xmlDOM) {
      // var xmlDOM = createXMLDOM();
      if (xmlDOM) {
        let graph = xmlDOM.createElement('graph');
        for (let i = 0; i < 2; i++) {
          let node = xmlDOM.createElement('set');
          node.setAttribute('name', i + 1);
          node.setAttribute('value', i + 1);
          let scriptNode = xmlDOM.createElement("Script");
          let newCDATA = xmlDOM.createCDATASection( "select * from t1");
          scriptNode.appendChild(newCDATA);
          node.appendChild(scriptNode);
          graph.appendChild(node);

        }
        xmlDOM.appendChild(graph);
        return this.parseXMLToString(xmlDOM);
      }
      return;
    },
    removeSelectedNode(){
      let that = this
      let delLine = []//记录要删除的连接线
      if(this.canvasPro.currentNodeObj instanceof window.JTopo.Node || this.canvasPro.currentNodeObj  instanceof window.JTopo.Container ) {
        console.info("删除当前选择的节点id：",this.canvasPro.currentNodeObj.modelPro.id)
        // console.info("删除当前选择的节点nodePro.v,id：",this.canvasPro.currentNodeObj.nodePro)
        let find = false
        let nodeType = null
        //删除fromNode
        for (let i1 = 0; i1 < this.globalNodeArray.length; i1++) {
          if (this.globalNodeArray[i1].modelPro.id===this.canvasPro.currentNodeObj.modelPro.id){
              nodeType = this.globalNodeArray[i1].modelPro.type
              this.globalNodeArray.splice(i1, 1);
              this.canvasPro.publicScene.remove(this.canvasPro.currentNodeObj.childs[0])
              this.canvasPro.publicScene.remove(this.canvasPro.currentNodeObj)
              this.paintingLinkPro.sourceNode=null
              find = true
              console.info("删除源节点后，this.globalNodeArray:",this.globalNodeArray)
              break
          }
        }
        if(find==true){

          let toNode = null
          let fromNode = null
          //删除from关联的线及设置输出节点的isLink为false
          for (let i2 = 0; i2 <this.globalLineArray.length ; i2++) {
            //如果删除的是from节点
                if (this.globalLineArray[i2].modelPro.from.indexOf(this.canvasPro.currentNodeObj.modelPro.id)!=-1){
                  //得到连接线的toNode节点
                  toNode = this.globalLineArray[i2].modelPro.to
                  //记录要删除的从当前节点输出的连接线
                  delLine.push( this.globalLineArray[i2])

                  console.info("删除关联线后，this.globalLineArray:",this.globalLineArray,"toNode:",toNode)
                  //如果是copy节点，就会有多个输出节点
                  //设置toNode 的isLink为false
                  if (toNode!=null){
                    for (let i3 = 0; i3 < this.globalNodeArray.length; i3++) {
                      if (this.globalNodeArray[i3].modelPro.id===toNode){
                        //将toNode的isLink设置为false
                        this.globalNodeArray[i3].modelPro.isLink=false
                        console.info("删除关联线后，修改toNode的isLink this.globalNodeArray:",this.globalNodeArray,"toNode:",toNode)
                          //如果是copy节点，会有多个输出，所以不能退出，要一直循环查询
                      }
                    }
                  }
                  if (nodeType!=null && nodeType!="COPY_STREAM"){
                    console.info("不是COPY_STREAM节点退出2，nodeType:"+nodeType)
                    break
                  }

                }


          }

          //当该节点属于目标节点时，删除to关联到该节点的线
          let find2 = false
          for (let i2 = 0; i2 <this.globalLineArray.length ; i2++) {
            //如果删除的是to输出节点
            if (this.globalLineArray[i2].modelPro.to===this.canvasPro.currentNodeObj.modelPro.id ){
              //查找from输入节点
              fromNode = this.globalLineArray[i2].modelPro.from

              //记录要删除的指向当前节点的连接线

              delLine.push( this.globalLineArray[i2])
              find2=true
              console.info("删除关联线后，this.globalLineArray:",this.globalLineArray,"fromNode:",fromNode)
              if (fromNode!=null){
                for (let i3 = 0; i3 < this.globalNodeArray.length; i3++) {
                  //将fromNode输入节点的isLink设置为false,注意transform节点不允许设置
                  //<Line id="LINE_01" type="STANDARD" from="DB_INPUT_01" to="TRANSFORM_01" order="1" metadata="METADATA_01" >线标注</Line>
                  //<Line id="LINE_02" type="TRANSFORM" from="TRANSFORM_01:0" to="TRANSFORM_02" order="2" metadata="METADATA_02">线标注</Line>
                  //<Line id="LINE_03" type="TRANSFORM" from="TRANSFORM_02:0" to="TRANSFORM_03" order="3" metadata="METADATA_02">线标注</Line>
                  //<Line id="LINE_04" type="TRANSFORM" from="TRANSFORM_03:0" to="DB_OUTPUT_01" order="4" metadata="METADATA_02">线标注</Line>
                  nodeType = this.globalNodeArray[i3].modelPro.type
                  if (this.globalNodeArray[i3].modelPro.id===fromNode){
                    // if(nodeType!="TRANSFORM"){
                      this.globalNodeArray[i3].modelPro.isLink=false
                      console.info("删除关联线后，修改fromNode的isLink this.globalNodeArray:",this.globalNodeArray,"fromNode:",fromNode)
                      break
                    // }else{
                    //   console.info("TRANSFORM的输入节点不设置isLink")
                    // }

                  }
                  if(nodeType==="TRANSFORM"){
                    //如果from输入节点为转换节点，multiWay设置为0
                    this.globalNodeArray[i3].modelPro.multiWay="0"

                  }
                }
              }
            break
            }
          }
          //删除from关联的线
          if (delLine.length>0){
            for (let i4 = 0; i4 < delLine.length; i4++) {
              for (let i2 = 0; i2 <this.globalLineArray.length ; i2++) {
                  if (delLine[i4].modelPro.id===this.globalLineArray[i2].modelPro.id){
                    this.canvasPro.publicScene.remove(this.globalLineArray[i2])
                    this.globalLineArray.splice(i2, 1);
                    console.info("删除连接线delLine[i4].modelPro.id:",delLine[i4].modelPro.id)
                    break
                  }
              }
            }
            console.info("删除连接线后的this.globalLineArray:",this.globalLineArray)
          }

        }
        this.canvasPro.currentNodeObj = null
        this.contextMenu.menuShow = 'none'
      }else   if(this.canvasPro.currentNodeObj instanceof window.JTopo.Link){
        console.info("删除当前选择的线id:", this.canvasPro.currentNodeObj.modelPro.id)


        let fromNode = null
        let toNode = null
        let nodeType = null
        let lineType = null
        for (let i1 = 0; i1 <this.globalLineArray.length ; i1++) {
          if (this.globalLineArray[i1].modelPro.id===this.canvasPro.currentNodeObj.modelPro.id){
              // from="COPY_STREAM_01:0" from="TRANSFORM_01:0"
              fromNode = this.globalLineArray[i1].modelPro.from.split(":")[0]
              toNode = this.globalLineArray[i1].modelPro.to
              lineType = this.globalLineArray[i1].modelPro.type
            //删除线数组中的线
              this.canvasPro.publicScene.remove(this.globalLineArray[i1])
              this.globalLineArray.splice(i1,1)
              if(lineType==="COPY"){
                for (let i4 = 0; i4 < this.globalLineCopyStreamArray.length; i4++) {
                    if( this.globalLineCopyStreamArray[i4].id.indexOf(toNode)!=-1){
                      this.globalLineCopyStreamArray.splice(i4,1)
                      console.info("删除线之后的,关联toNode",toNode,"删除 this.globalLineCopyStreamArray：", this.globalLineCopyStreamArray)
                      break
                    }
                }
              }
              break
          }
        }
        console.info("删除线之后的 this.globalLineArray：", this.globalLineArray)
        console.info("删除线时，准备删除fromNode:",fromNode)
        console.info("删除线时，准备删除toNode:",toNode)
        //设置线中from和to 输入及输出节点的isLink，如果from输入节点为转换节点，multiWay设置为0
        //设置fromNode   from="COPY_STREAM_01:0" from="TRANSFORM_01:0"
        for (let i2 = 0; i2  < this.globalNodeArray.length; i2++) {
          if (this.globalNodeArray[i2].modelPro.id===fromNode){
            nodeType = this.globalNodeArray[i2].modelPro.type

            if(nodeType==="TRANSFORM"){
              //from是转换节点时不设置isLink，但要设置multiWay
              this.globalNodeArray[i2].modelPro.multiWay="0"
            }else{
              this.globalNodeArray[i2].modelPro.isLink=false
            }
            break
          }
        }
        //设置toNode
        for (let i2 = 0; i2  < this.globalNodeArray.length; i2++) {
          if (this.globalNodeArray[i2].modelPro.id===toNode){
            this.globalNodeArray[i2].modelPro.isLink=false
            break
          }
        }
        console.info("删除fromNode和toNode后的 this.globalNodeArray：", this.globalNodeArray)


        this.canvasPro.currentNodeObj = null
        this.contextMenu.menuShow = 'none'
      }

    },
  //设置右键菜单位置
    showContextMenu(event){
      // console.info("event.y:",event.y,"event.x:",event.x,"event.pageX:",event.pageX,"event.pageY:",event.pageY)
      this.contextMenu.menuTop = event.y+'px'//event.pageY+'px'//
      this.contextMenu.menuLeft = event.x+'px'//event.pageX+'px'//
