      if(event.target instanceof window.JTopo.Node || event.target instanceof window.JTopo.Container ) {
        console.info("当前选择的节点id:", event.target.modelPro.id)
      }else   if(event.target instanceof window.JTopo.Link){
        console.info("当前选择的线id:", event.target.modelPro.id)

      }

    },
