    },
    connectTabClick(row) {
      console.log('值：',row.k);
    },
    //解析当前选择的组件类型
    loadProperties(e){
      let that = this;
      console.info("loadProperties:",e)
      // if (e==="DB_INPUT_TABLE"){
      if (e===this.nodeType.DB_INPUT_TABLE){
        // that.nodeTableData =that.dbInputTable// [{"k":"id","v":"string"},{"k":"type","v":"string"}]
        // that.canvasPro.currentNodeType = 'DB_INPUT_TABLE'
        that.canvasPro.currentNodeType = this.nodeType.DB_INPUT_TABLE
        that.canvasPro.currentNodeText = '读数据表'
        that.canvasPro.currentNodePro = that.dbInputTable
        that.canvasPro.currentNodeModelPro = {"type":this.nodeType.DB_INPUT_TABLE,"text":'读数据表',"isLink":false}
      // }else if(e==="DB_OUTPUT_TABLE"){
      }else if(e===this.nodeType.DB_OUTPUT_TABLE){
        // that.nodeTableData =that.dbOutputTable
        that.canvasPro.currentNodeType = this.nodeType.DB_OUTPUT_TABLE
        that.canvasPro.currentNodeText = '写数据表'

        that.canvasPro.currentNodePro = that.dbOutputTable
        that.canvasPro.currentNodeModelPro = {"type":this.nodeType.DB_OUTPUT_TABLE,"text":'写数据表',"isLink":false}
      }else if(e===this.nodeType.XLS_READER){
        that.canvasPro.currentNodeType = this.nodeType.XLS_READER
        that.canvasPro.currentNodeText = '读EXCEL'
