    newTopo() {
      let that = this;

      if ( that.canvasPro.publicScene !=null){

        that.canvasPro.publicStage.remove( that.canvasPro.publicScene);
        that.canvasPro.publicStage.clear();
        that.canvasPro.publicStage.frames = 0; // 避免新建画布的屏幕闪

        that.canvasPro.currentNodeType = null
        that.canvasPro.publicScene = null
        that.canvasPro.publicStage = null
        that.canvasPro.currentNodeObj=null
        that.nodeTableData = null
        that.globalNodeArray=[]
        that.globalLineArray=[]
        that.globalLineCopyStreamArray=[]
        that.globalMetadataArray=[]
        that.globalDbConnectArray=[]
        that.paintingLinkPro.sourceNode=null

      }


      let canvas = document.getElementById("canvas");
      let stage = new JTopo.Stage(canvas);
      // stage.eagleEye.visible = true;
      stage.eagleEye.visible = that.canvasPro.eagleEyeEnable;
      // stage.wheelZoom = 1.2; //缩放比例
      let scene = new JTopo.Scene(stage);
      // scene.background = '/static/bg.png';
      scene.alpha = 1;
      // scene.backgroundColor = '63,74,89';
      scene.backgroundColor = '155, 158, 162';
      //例如：stage.mode = "drag";
      //normal[默认]：可以点击选中单个节点（按住Ctrl可以选中多个），点中空白处可以拖拽整个画面
      //drag: 该模式下不可以选择节点，只能拖拽整个画面
      //nodrag :不允许拖拽
      //select: 可以框选多个节点、可以点击单个节点
      //edit: 在默认基础上增加了：选中节点时可以通过6个控制点来调整节点的宽、高
      scene.mode= that.canvasPro.sceneModel;//
      // scene.areaSelect = false;//是否允许多选

      that.canvasPro.publicScene = scene
      that.canvasPro.publicStage = stage
      // that.canvasPro.publicScene.addEventListener('mousedown', function (event) {
      //   if(event.target instanceof window.JTopo.Node){
      //     console.info("按下鼠标：",event.target.modelPro.id)
      //   }
      // })
      //鼠标松开,只要松开就删除临时线
      that.canvasPro.publicScene.addEventListener('mouseup', function (event) {

        if (event.target==null) {
          // console.info("在场景上松开鼠标")
          that.contextMenu.menuShow = 'none'
        }else{
          if( event.target instanceof window.JTopo.Container){
            // console.info("拖动节点：x:",event.x,"y:",event.y)
            if (event.target.modelPro!=null){
              // console.info("鼠标松开：",event.target.modelPro.text )
              event.target.modelPro.x = event.target.x//节点本身记载了自己的坐标
              event.target.modelPro.y =  event.target.y
            }

          }

          if (event.button == 2) {
            // console.info("右键删除临时线")
            if(that.paintingLinkPro.tmpBeginNode!=null && that.paintingLinkPro.tmpLinkNode!=null){
              if(that.paintingLinkPro.mouseDragX!=null && that.paintingLinkPro.mouseDragY!=null){
                //松开鼠标后给x,y赋值为拖动节点的x,y
                that.paintingLinkPro.mouseDragX = that.paintingLinkPro.tmpBeginNode.x
                that.paintingLinkPro.mouseDragY =   that.paintingLinkPro.tmpBeginNode.y
              }
              that.canvasPro.publicScene.remove(that.paintingLinkPro.tmpBeginNode)
              that.canvasPro.publicScene.remove(that.paintingLinkPro.tmpLinkNode)
              that.paintingLinkPro.tmpBeginNode=null
              that.paintingLinkPro.tmpLinkNode=null

            }
          //弹出菜单框
            if (event.target.modelPro!=null && (event.target instanceof window.JTopo.Node || event.target instanceof window.JTopo.Container  ) ){
              // console.info("右键点击节点或容器")
              that.showContextMenu(event)
            }else if (event.target.modelPro!=null && event.target instanceof window.JTopo.Link){
              // console.info("右键点击连线")
              that.showContextMenu(event)
            }
          }else{
            // console.info("左键抬起删除临时线")
            if(that.paintingLinkPro.tmpBeginNode!=null && that.paintingLinkPro.tmpLinkNode!=null){
              if(that.paintingLinkPro.mouseDragX!=null && that.paintingLinkPro.mouseDragY!=null){
                //松开鼠标后给x,y赋值为拖动节点的x,y
                that.paintingLinkPro.mouseDragX = that.paintingLinkPro.tmpBeginNode.x
                that.paintingLinkPro.mouseDragY =   that.paintingLinkPro.tmpBeginNode.y
              }
              that.canvasPro.publicScene.remove(that.paintingLinkPro.tmpBeginNode)
              that.canvasPro.publicScene.remove(that.paintingLinkPro.tmpLinkNode)
              that.paintingLinkPro.tmpBeginNode=null
              that.paintingLinkPro.tmpLinkNode=null


            }
          }


        }





      })
      //鼠标经过(先执行松鼠标再进入到经过)
      that.canvasPro.publicScene.addEventListener('mouseover',function (event) {
// console.info("event.button:",event.button,"that.paintingLinkPro.sourceNode :",that.paintingLinkPro.sourceNode )

        // return
        if (event.target==null){
          // console.info("鼠标经过event.target.modelPro为空")
        }else if(event.target.modelPro!=null ){
          // console.info("鼠标经过event.target.modelPro:",event.target.modelPro.text)
          // console.info("鼠标经过that.paintingLinkPro.mouseDragX:",that.paintingLinkPro.mouseDragX)
          // console.info("鼠标经过that.paintingLinkPro.mouseDragY:",that.paintingLinkPro.mouseDragY)
          // console.info("鼠标经过event.x:",event.x,"event.y",event.y)
          if (that.paintingLinkPro.mouseDragX==null){
            return
          }
          //源节点ID跟目标节点ID不同，type不同
          if (
            ( (that.paintingLinkPro.mouseDragX==event.x) && //精确定位目标节点位置
              (that.paintingLinkPro.mouseDragY==event.y )) &&//精确定位目标节点位置
            that.paintingLinkPro.sourceNode!==null && event.target!==null &&
            event.target.modelPro!==null && that.paintingLinkPro.sourceNode.modelPro.id!==event.target.modelPro.id
            // && (that.paintingLinkPro.sourceNode.modelPro.type!==event.target.modelPro.type && that.paintingLinkPro.sourceNode.modelPro.type!="TRANSFORM")
          ){
            // console.info("鼠标经过的目标节点：",event.target.modelPro.id,event.target.modelPro.text)



            //添加连线
            let nodeTo = that.findNodeIdForArray(event.target.modelPro.id)

            // console.info("连线查询nodeTo:",nodeTo)
            let _link_ = that.paintinglinkLogic(that.paintingLinkPro.sourceNode.modelPro,nodeTo.modelPro)

            //证明画线成功
            if(_link_!=null){
              //画完线后重新初始化要连接目标节点时的必要条件
              that.paintingLinkPro.sourceNode = null
              that.paintingLinkPro.mouseDragX=null
              that.paintingLinkPro.mouseDragY=null
            }
          }


        }

      })
      //鼠标拖动
      that.canvasPro.publicScene.addEventListener('mousedrag',function (event) {


        if(that.paintingLinkPro.tmpBeginNode!=null){

          // console.info("event.x:",event.x,"event.y:",event.y,"event.target:",(event.target.modelPro!=null?event.target.modelPro.text:"event.target.modelPro为空"))
          that.paintingLinkPro.tmpBeginNode.setLocation(event.x, event.y)
        }


      })

    },
    //画节点
    drawNode(e){
      let that = this
      if (that.canvasPro.publicScene!=null){
        if (that.canvasPro.currentNodeType!=null){
          that.canvasPro.currentMouseX = e.offsetX// e.pageX//鼠标X坐标
          that.canvasPro.currentMouseY = e.offsetY//e.pageY//鼠标Y坐标
          // console.info("canvasPro.currentMouseX:",that.canvasPro.currentMouseX)
          // console.info("canvasPro.currentMouseY:",that.canvasPro.currentMouseY)

          ////////node start///////
          // var node = new JTopo.Node(that.canvasPro.currentNodeType);
          // node.fillColor = '0,0,255';
          // // node.setLocation(409, 269);
          // //node.setImage('/static/bg.png', true);
          // node.setLocation(that.canvasPro.currentMouseX, that.canvasPro.currentMouseY);
          // node.setSize(this.NODE_PROPERTIES.width, this.NODE_PROPERTIES.height);//大小
          that.canvasPro.currentNodeModelPro.x=that.canvasPro.currentMouseX
          that.canvasPro.currentNodeModelPro.y=that.canvasPro.currentMouseY

          // console.info("添加前that.globalNodeArray.length:",that.globalNodeArray.length)
          //全局全新空数组，注意使用深度clone数组的方式
          let _nodePro_ = JSON.parse(JSON.stringify(that.canvasPro.currentNodePro));
          if  (that.globalNodeArray.length==0){
            that.canvasPro.currentNodeModelPro.sortId= 1
            that.canvasPro.currentNodeModelPro.type= that.canvasPro.currentNodeType
            that.canvasPro.currentNodeModelPro.id= that.canvasPro.currentNodeType+"_"+1
            that.canvasPro.currentNodeModelPro.multiWay="0"

            _nodePro_ = that.synchNodeProFromKV(_nodePro_,"id",that.canvasPro.currentNodeModelPro.id)

          }else{
            //全局数组中该类型节点不存在
            let tmp = that.findNodeTypeForArray(that.canvasPro.currentNodeType)
            if (tmp.length==0){
              that.canvasPro.currentNodeModelPro.sortId= 1
              that.canvasPro.currentNodeModelPro.type= that.canvasPro.currentNodeType
              that.canvasPro.currentNodeModelPro.id= that.canvasPro.currentNodeType+"_"+1
              that.canvasPro.currentNodeModelPro.multiWay="0"

              _nodePro_ = that.synchNodeProFromKV(_nodePro_,"id",that.canvasPro.currentNodeModelPro.id)
            }else{
              //全局数组中该类型节点存在
              tmp = that.sortKey(tmp,"sortId")
              // console.info("该类型已存在that.globalNodeArray :",tmp ,"that.globalNodeArray[0].modelPro.id：",tmp[0].modelPro.id)
              that.canvasPro.currentNodeModelPro.sortId =  tmp[0].modelPro.sortId+1
              that.canvasPro.currentNodeModelPro.type= that.canvasPro.currentNodeType

              that.canvasPro.currentNodeModelPro.id= that.canvasPro.currentNodeType+"_" +  that.canvasPro.currentNodeModelPro.sortId
              that.canvasPro.currentNodeModelPro.multiWay="0"
              // console.info("that.canvasPro.currentNodeModelPro.id:",that.canvasPro.currentNodeModelPro.id)
              _nodePro_ = that.synchNodeProFromKV(_nodePro_,"id",that.canvasPro.currentNodeModelPro.id)

            }

          }

          var container = this.createContainer(that.canvasPro.currentMouseX,that.canvasPro.currentMouseY,
            this.NODE_PROPERTIES.width, this.NODE_PROPERTIES.height,
            that.canvasPro.currentNodeText,_nodePro_,that.canvasPro.currentNodeModelPro)
            // that.canvasPro.currentNodeText,that.canvasPro.currentNodePro,that.canvasPro.currentNodeModelPro)
          // that.canvasPro.publicScene.add(node);
          // that.canvasPro.publicScene.add(container);

          ////////node end///////
          that.globalNodeArray.push(container)//将节点增加到全局数组中
          that.canvasPro.currentNodeType = null
          // console.info("添加后that.globalNodeArray：",that.globalNodeArray)
        }else{
          console.info('请先选择一个节点。')
        }
      }else{
        console.info('请先创建一个新配置。')
      }
    },
    createContainer(x,y,w,h,text,nodePro,modelPro){
      var that = this;
      // var container = new JTopo.Container(text);
      var container = new JTopo.Container();
      container.textPosition = 'Middle_Center';
      container.fontColor = '100,255,0';
      container.font = '14pt 微软雅黑';
      let findSourceType = this.isInPutNode(modelPro.type)
      let findTargetType = this.isOutPutNode(modelPro.type)
      let findType = this.isTransformNode(modelPro.type)

      if (findSourceType==true){
        container.fillColor = '200,167,9';//黄色，输入节点
      }else if(findTargetType==true){

        // container.fillColor = '117,141,240';//蓝色，输出节点
        container.fillColor = '0,230,230';//蓝色，输出节点
      }else if(findType==true){
        container.fillColor = '4,198,33';//绿色，转换节点
      }else{
        container.fillColor = '104,198,33';//
      }

      container.borderColor = '0,0,0';//黑色
      container.borderWidth = 1; // 边框的宽度

      // container.borderRadius = 30; // 圆角
      container.alpha = 1;//透明度
      container.setBound(x, y, w, h);
      container.nodePro = nodePro//自定义属性
      container.modelPro = modelPro//自定义属性

      // 流式布局（水平、垂直间隔均为10)
      // var flowLayout = JTopo.layout.FlowLayout(10, 10);
      // 网格布局(4行3列)
      var gridLayout = JTopo.layout.GridLayout(1, 1);
      //容器默认是没有固定大小的，是通过布局来实现的定位
      container.layout = gridLayout;
      let n1 = this.createNode(text,10,10,that.NODE_PROPERTIES.width,that.NODE_PROPERTIES.height,nodePro,modelPro);


      container.add(n1);
      //记录父节点,用于画线时使用
      n1.parentNode = container

      container.addEventListener("dbclick", function(event){
        // container.dragable = !container.dragable;//是否允许拖动。
        that.canvasPro.currentContainer = event.target;

        // console.info("nodePro："+JSON.stringify(that.canvasPro.currentContainer.nodePro));
        // console.info("modelPro："+JSON.stringify(that.canvasPro.currentContainer.modelPro));
        // that.$message({
        //   type: 'info',
        //   showClose: true,
        //   duration:0,
        //   dangerouslyUseHTMLString: true,
        //   // iconClass:'el-icon-info',
        //   message: '<spand style="color:black;"> 已经选择的ID：'+this.currentContainer.text +"</spand>"
        // });

      });

      container.addEventListener('click',function(event){
         // console.info("event.target.nodePro:",event.target.nodePro)
         // console.info("event.target.modelPro:",event.target.modelPro)
        //table编辑内容
         that.nodeTableData = event.target.nodePro

      });
      // scene.add(container);
      that.canvasPro.publicScene.add(container);


      return container;
    },
    createNode(text,x,y,w,h,nodePro,modelPro){
      var that = this;
      // let node = new JTopo.Node(text);
      // var node = new JTopo.CircleNode(text);
      var node = new JTopo.TextNode(text);
      // var node = new JTopo.LinkNode(text);
      // var node = new JTopo.PieChartNode(text);
      node.setLocation(x, y);
      node.setSize(w, h);//大小
      node.textPosition = 'Middle_Center';
      node.fontColor = '0,0,0';//'255,255,255';//白色
      node.font = '11pt 微软雅黑';
      node.dragable = false;
        // node.showSelected = true;
      // node.showSelected = false; // 不显示选中矩形
      // node.paintSelected = function (a){	//修改节点选中样式
      //   0 != this.showSelected &&
      //   (a.save(), a.beginPath(), a.strokeStyle = "rgba(218,221,221, 0.9)",
      //     a.fillStyle = "rgba(218,221,221,0)",
      //     a.arc( 0, 0, this.width/2 + 3, 0, Math.PI*2, false),
      //     a.fill(), a.stroke(), a.closePath(), a.restore())
      // };
      node.nodePro = nodePro//自定义属性
      node.modelPro = modelPro//自定义属性
