    /*
     画线的业务逻辑,
     @param sourceNodeModelPro 源节点modelPro
     @param targetNodeModelPro 目标节点modelPro
     @return 线对象
     */
    paintinglinkLogic (sourceNodeModelPro,targetNodeModelPro) {
      let that = this;
      // if (e.target != null && e.target instanceof window.JTopo.Node) {
      //   console.info("offsetX:",e.offsetX,"offsetY",e.offsetY,"text:",e.target.text)
      // }
      let sourceType = sourceNodeModelPro.type
      let sourceId = sourceNodeModelPro.id
      let sourceIsLink = sourceNodeModelPro.isLink
      //只在copystream时有用,默认是0代表没输出，输出一次累加1，只有copystream允许大于1，transform最大只能等于1，输入节点和输出节点只能是0
      let sourceMultiWay = sourceNodeModelPro.multiWay
      let targetType = targetNodeModelPro.type
      let targetId = targetNodeModelPro.id
      let targetIsLink = targetNodeModelPro.isLink


      let findSType = 1
      let findTType = 1

      findSType = this.isInPutNodeExt(sourceType)
      findTType = this.isOutPutNodeExt(targetType)

      // console.info("findSType:",findSType,"findTType:",findTType)
      if (findSType==1){
        console.info("输入节点类型不匹配")
        return null
      }
      if (findTType==1){
        console.info("输出节点类型不匹配")
        return null
      }
      //符合连接线的原则
      if ((findSType!=1) && (findTType!=1)){
          if(findSType==2 && findTType==2) {//输入节点 和 输出节点
            if (sourceIsLink==true ){
              console.info("输入节点已经连接过")
              return null
            } else if(sourceIsLink==false && targetIsLink==false){

              return that.drawLink(sourceId,targetId,"STANDARD")
            }else{
              console.info("输入节点向输出节点-无法连接")
              return null
            }

          }else if(findSType==2 && findTType==3){//输入节点 和 transform转换节点
            if (sourceIsLink==true ){
              console.info("[输入节点]已经连接过")
              return null
            }else if(sourceIsLink==false && targetIsLink==false){
              return that.drawLink(sourceId,targetId,"STANDARD")
            }else{
              console.info("[输入节点]向[transform转换]-无法连接")
              return null
            }
          }else if(findSType==3 && findTType==3){//transform转换节点 和 transform转换节点
            if (targetIsLink==true ){
              console.info("[transform]连接的[transform]已经连接过")
              return null
            }else if(sourceMultiWay=="0" && targetIsLink==false && sourceIsLink==true){
              //必须遵循先从输入节点 -> TRANSFROM节点 -> TRANSFROM节点 -> TRANSFROM节点 -> 输出节点 的原则，不能直接从 TRANSFROM节点 -> 输出节点 或 TRANSFROM节点 -> TRANSFROM节点
              let _link_ = that.drawLink(sourceId,targetId,"TRANSFORM")
              //画完连接线后，设置sourceNodeModelPro.multiWay="1"
              that.setMultiWayForNodeArray(sourceId)
              //给目标TRANSFROM节点设置isLink
              that.setIsLinkForNodeArray(targetId)
              return _link_
            }else{
              console.info("[transform]向[transform]-无法连接")
              return null
            }
          }else if(findSType==3 && findTType==2){//transform转换节点 和 输出节点
            if (targetIsLink==true ){
              console.info("[transform]连接的[输出节点]已经连接过")
              return null
            }else  if  (sourceMultiWay=="0" && targetIsLink==false ){
              let _link_ = that.drawLink(sourceId,targetId,"TRANSFORM")
              //画完连接线后，设置sourceNodeModelPro.multiWay="1"
              that.setMultiWayForNodeArray(sourceId)
              return _link_
            }else{
              console.info("[transform]向[输出节点]-无法连接")
              return null
            }
          }else if(findSType==2 && findTType==4){//输入节点 和 copystream转换节点
            if (sourceIsLink==true ){
              console.info("[输入节点]已经连接过")
              return null
            }else if(sourceIsLink==false && targetIsLink==false){
              return that.drawLink(sourceId,targetId,"STANDARD")
            }else{
              console.info("[输入节点]向[copystream转换]-无法连接")
              return null
            }
          }else if(findSType==4 && findTType==2) {//copystream转换节点 和 输出节点
            if (targetIsLink==true ){
              console.info("[copystream]连接的[输出节点]已经连接过")
              return null
            }else if(targetIsLink==false ){
              //copystream只能连接输出节点，不能连接transform节点
              return that.drawLink(sourceId,targetId,"COPY")
            }
          }else{
            console.info("不支持[" + sourceType + "]与[" + targetType + "]连接")
            return null
          }
      }
    },
    /*
    * @param _edgeType_ STANDARD | TRANSFORM  | COPY
    * */
    drawLink(sourceId,targetId,_edgeType_){
      let that = this
      let nodeTo = that.findNodeIdForArray(targetId);
      let link = new JTopo.Link(that.paintingLinkPro.sourceNode.parentNode, nodeTo );//曲线
      // let link = new JTopo.Link(that.paintingLinkPro.sourceNode.parentNode, nodeTo );//曲线
      // let link = new JTopo.CurveLink(that.paintingLinkPro.sourceNode.parentNode, nodeTo);//曲线
      // let link = new JTopo.FlexionalLink(that.paintingLinkPro.sourceNode.parentNode, nodeTo, "连接");//曲线
      // let link = new JTopo.FoldLink(that.paintingLinkPro.sourceNode.parentNode, nodeTo, "连接");//曲线
      link.toArrowSize = 30;
      // link.fromArrowSize = 10;
      link.arrowsRadius = 10; //箭头大小
      link.strokeColor = '0,0,255';//线条颜色
      link.bundleOffset = 60; // 折线拐角处的长度
      link.bundleGap = 20; // 线条之间的间隔
      link.zIndex = 0//让连接线显示在节点的后面
      that.canvasPro.publicScene.add(link)
      // link.modelPro = {"from":sourceId,"to":targetId,"type":"STANDARD","order":"0","metadata":""}
      if (_edgeType_=="STANDARD"){
        link.modelPro = {"from":sourceId,"to":targetId,"type":_edgeType_,"order":"0","metadata":""}

        link.nodePro = [
          {"k":"from","flag":"text","v":sourceId,"edit":"false","show":"true"},
          {"k":"to","flag":"text","v":targetId,"edit":"false","show":"true"},
          {"k":"type","flag":"text","v":_edgeType_,"edit":"false","show":"true"},
          {"k":"order","flag":"text","v":"0","edit":"true","show":"true"},
          {"k":"metadata","flag":"metaComBox","v":"","edit":"true","show":"true"},
        ]
      }else if(_edgeType_=="TRANSFORM"){
        link.modelPro = {"from":sourceId+":0","to":targetId,"type":_edgeType_,"order":"0","metadata":""}

        link.nodePro = [
          {"k":"from","flag":"text","v":sourceId+":0","edit":"false","show":"true"},
          {"k":"to","flag":"text","v":targetId,"edit":"false","show":"true"},
          {"k":"type","flag":"text","v":_edgeType_,"edit":"false","show":"true"},
          {"k":"order","flag":"text","v":"0","edit":"true","show":"true"},
          {"k":"metadata","flag":"metaComBox","v":"","edit":"true","show":"true"},
        ]

      }else if(_edgeType_=="COPY"){
        ///////////////

        //全局数组中该类型节点不存在

        let tmp = that.findNodeIdForLineCopyStreamArray(sourceId)
        if (tmp.length==0){
          link.modelPro = {"from":sourceId+":1","to":targetId,"type":_edgeType_,"order":"0","metadata":""}

          link.nodePro = [
            {"k":"from","flag":"text","v":sourceId+":1","edit":"false","show":"true"},
            {"k":"to","flag":"text","v":targetId,"edit":"false","show":"true"},
            {"k":"type","flag":"text","v":_edgeType_,"edit":"false","show":"true"},
            {"k":"order","flag":"text","v":"0","edit":"true","show":"true"},
            {"k":"metadata","flag":"metaComBox","v":"","edit":"true","show":"true"},
          ]
          that.globalLineCopyStreamArray.push({"id":sourceId+":1","sortId":1})
          // console.info("that.globalLineCopyStreamArray:",that.globalLineCopyStreamArray)
        }else{
          // console.info("返回tmp:",tmp)
          //全局数组中该类型节点存在
          tmp = that.sortKeyForCopyStream(tmp,"sortId")
          let sortId = tmp[0].sortId + 1
          let id =  sourceId +":"+ sortId
          link.modelPro = {"from":id,"to":targetId,"type":_edgeType_,"order":"0","metadata":""}

          link.nodePro = [
            {"k":"from","flag":"text","v":id,"edit":"false","show":"true"},
            {"k":"to","flag":"text","v":targetId,"edit":"false","show":"true"},
            {"k":"type","flag":"text","v":_edgeType_,"edit":"false","show":"true"},
            {"k":"order","flag":"text","v":"0","edit":"true","show":"true"},
            {"k":"metadata","flag":"metaComBox","v":"","edit":"true","show":"true"},
          ]

          that.globalLineCopyStreamArray.push({"id":id ,"sortId":sortId})
        }
        ////////////
      }


      ///
      //全局全新空数组
      if  (that.globalLineArray.length==0){
        link.modelPro.sortId = 1
        link.modelPro.id = "LINE_" + _edgeType_ + "_" + 1
        // link.type = "STANDARD"
        link.type = _edgeType_
      }else{
        //全局数组中该类型节点不存在
        // let tmp = that.findLineTypeForArray("STANDARD")
        let tmp = that.findLineTypeForArray(_edgeType_)
        if (tmp.length==0){
          link.modelPro.sortId = 1
          link.modelPro.id = "LINE_"  + _edgeType_ + "_" + 1
          // link.type = "STANDARD"
          link.type = _edgeType_
        }else{
          //全局数组中该类型节点存在
          tmp = that.sortKey(tmp,"sortId")
          link.modelPro.sortId = tmp[0].modelPro.sortId + 1
          link.modelPro.id = "LINE_"  + _edgeType_ + "_" +  link.modelPro.sortId
          // link.type = "STANDARD"
          link.type = _edgeType_
        }

      }
      ///
      //如果源是COPY_STREAM则不设置isLink，如果源是非COPY_STREAM则设置isLink,原因是COPY_STREAM可以向任意节点输出，但只能有一个输入节点指向COPY_STREAM
      if (_edgeType_!="COPY" && _edgeType_!="TRANSFORM"){
        that.setIsLinkForNodeArray(sourceId)
      }

      that.setIsLinkForNodeArray(targetId)
      that.globalLineArray.push(link)
      // console.info("drawLink::that.globalLineArray:",that.globalLineArray)
      link.addEventListener('click',function(event){
        // console.info("event.target.nodePro:",event.target.nodePro)
        // console.info("event.target.modelPro:",event.target.modelPro)
        //table编辑内容
        that.nodeTableData = event.target.nodePro

      });
      return link
    },
    isInPutNode(sourceType){
      let findSourceType = false
      for (let i1 = 0; i1 <this.INPUT_NODE_TYPE.length ; i1++) {
        if (this.INPUT_NODE_TYPE[i1].indexOf(sourceType)!=-1){
          findSourceType = true
          // console.info("输入节点：",findSourceType)
          break
        }
      }
      return findSourceType
    },
    isInPutNodeExt(sourceType){
      let findSourceType = 1
      for (let i1 = 0; i1 <this.INPUT_NODE_TYPE.length ; i1++) {
        if (this.INPUT_NODE_TYPE[i1].indexOf(sourceType)!=-1){
          findSourceType = 2//输入节点
          break
        }
      }
      if (findSourceType==1){
        for (let i1 = 0; i1 <this.TRANSFORM_NODE_TYPE.length ; i1++) {
          if (this.TRANSFORM_NODE_TYPE[i1].indexOf(sourceType)!=-1){
            findSourceType = 3//transform转换节点
            break
          }
        }
      }
      if (findSourceType==1){
        for (let i1 = 0; i1 <this.COPYSTREAM_NODE_TYPE.length ; i1++) {
          if (this.COPYSTREAM_NODE_TYPE[i1].indexOf(sourceType)!=-1){
            findSourceType = 4//copystream转换节点
            break
          }
        }
      }

      return findSourceType
    },
    isOutPutNode(targetType){
      let findTargetType = false
      for (let i1 = 0; i1 <this.OUTPUT_NODE_TYPE.length ; i1++) {
        if (this.OUTPUT_NODE_TYPE[i1].indexOf(targetType)!=-1){
          findTargetType = true
          break
        }
      }
      return findTargetType
    },
    isOutPutNodeExt(targetType){
      let findTargetType = 1
      for (let i1 = 0; i1 <this.OUTPUT_NODE_TYPE.length ; i1++) {
        if (this.OUTPUT_NODE_TYPE[i1].indexOf(targetType)!=-1){
          findTargetType = 2//输出节点
          break
        }
      }
      if (findTargetType==1){
        for (let i1 = 0; i1 <this.TRANSFORM_NODE_TYPE.length ; i1++) {
          if (this.TRANSFORM_NODE_TYPE[i1].indexOf(targetType)!=-1){
            findTargetType = 3 //transform转换节点
            break
          }
        }
      }
      if (findTargetType==1){
        for (let i1 = 0; i1 <this.COPYSTREAM_NODE_TYPE.length ; i1++) {
          if (this.COPYSTREAM_NODE_TYPE[i1].indexOf(targetType)!=-1){
            findTargetType = 4 //copystream转换节点
            break
          }
        }
      }
      return findTargetType
    },
    isTransformNode(type){
      let findType = false
      for (let i1 = 0; i1 <this.TRANSFORM_NODE_TYPE.length ; i1++) {
        if (this.TRANSFORM_NODE_TYPE[i1].indexOf(type)!=-1){
          findType = true
          break
        }
      }
      if (findType==false){
        for (let i1 = 0; i1 <this.COPYSTREAM_NODE_TYPE.length ; i1++) {
          if (this.COPYSTREAM_NODE_TYPE[i1].indexOf(type)!=-1){
            findType = true
            break
          }
        }
      }
      return findType
    },
    //json数组对象排序
    sortKey(array,key){
      return array.sort(function(a,b){
        if(!a.modelPro.hasOwnProperty(key)){
          a.modelPro[key]=0;
        }
        if(!b.modelPro.hasOwnProperty(key)){
          b.modelPro[key]=0;
        }
        var x = parseInt(a.modelPro[key]);
        var y = parseInt(b.modelPro[key]);
        // return ((x<y)?-1:(x>y)?1:0)
        return ((x<y)?1:(x>y)?-1:0)


      })
    },
    sortKey2(array,key){
      return array.sort(function(a,b){
        if(!a.hasOwnProperty(key)){
          a[key]=0;
        }
        if(!b.hasOwnProperty(key)){
          b[key]=0;
        }
        var x = parseInt(a[key]);
        var y = parseInt(b[key]);
        // return ((x<y)?-1:(x>y)?1:0)
        return ((x<y)?1:(x>y)?-1:0)


      })
    },
    //copyStream线数组中对sortId进行排序
    sortKeyForCopyStream(array,key){
      return array.sort(function(a,b){
        if(!a.hasOwnProperty(key)){
          a[key]=0;
        }
        if(!b.hasOwnProperty(key)){
          b[key]=0;
        }
        var x = parseInt(a[key]);
        var y = parseInt(b[key]);
        // return ((x<y)?-1:(x>y)?1:0)//asc
        return ((x<y)?1:(x>y)?-1:0)//desc


      })
    },
    //根据id查找copyStream线数组中包含该id的元素
    findNodeIdForLineCopyStreamArray(id){
      let tmp = []
      for(let i1=0;i1<this.globalLineCopyStreamArray.length;i1++){
        //找到ID对应的数组元素返回,copy节点会有多个元素
        if (this.globalLineCopyStreamArray[i1].id.indexOf(id)!=-1 ){
          tmp.push( this.globalLineCopyStreamArray[i1])

          // console.info("findNodeIdForLineCopyStreamArray:",this.globalLineCopyStreamArray[i1].id)

        }
      }

      return tmp
    },
    //根据节点类型查找相应的节点数组
    findNodeTypeForArray(type){
      let tmpArray =[]
      for(let i1=0;i1<this.globalNodeArray.length;i1++){
        //找到后累加
        if (this.globalNodeArray[i1].modelPro.type===type){
          tmpArray.push(this.globalNodeArray[i1])
        }
      }
      return tmpArray
    },
    //根据节点ID查找并返回该节点
    findNodeIdForArray(id){
      let tmp = null
       for(let i1=0;i1<this.globalNodeArray.length;i1++){
        //找到ID对应的节点返回
         if (this.globalNodeArray[i1].modelPro.id===id){
           tmp = this.globalNodeArray[i1]

           // console.info("findNodeIdForArray:",this.globalNodeArray[i1].modelPro.id)
           break
         }
       }

      return tmp
    },
    //通过类型查找相应的线数组
    findLineTypeForArray(type){
      let tmpArray =[]
      for(let i1=0;i1<this.globalLineArray.length;i1++){
        //
        if (this.globalLineArray[i1].modelPro.type===type){
          tmpArray.push(this.globalLineArray[i1])
        }
      }
      return tmpArray
    },
    //根据节点ID查找并设置已经连接线标志，并返回所有节点数组
    setIsLinkForNodeArray(id){
      for(let i1=0;i1<this.globalNodeArray.length;i1++){
        //找到后设置true
        if (this.globalNodeArray[i1].modelPro.id===id){
          this.globalNodeArray[i1].modelPro.isLink=true
          // console.info("setValueFindNodeIdForArray:",this.globalNodeArray[i1].modelPro.id)
          break
        }
      }

      // console.info("setValueFindNodeIdForArray:",this.globalNodeArray)
      return this.globalNodeArray
    },
    //供transform节点使用：根据节点ID查找并设置multiWay=1，并返回所有节点数组
    setMultiWayForNodeArray(id){
      for(let i1=0;i1<this.globalNodeArray.length;i1++){
        //找到后设置为1
        if (this.globalNodeArray[i1].modelPro.id===id){
          this.globalNodeArray[i1].modelPro.multiWay="1"
          // console.info("setMultiWayForNodeArray:",this.globalNodeArray[i1].modelPro.id)
          break
        }
      }

      // console.info("setMultiWayForNodeArray:",this.globalNodeArray)
      return this.globalNodeArray
    },
    //替换nodePro对应的key的value
    synchNodeProFromKV(nodePro,key,value){
      let find = false

      for (let i1 = 0; i1 <nodePro.length ; i1++) {
         if (nodePro[i1].k==key) {
             // Object.defineProperty(nodePro[i1], "v", {
             //   value: value
             // })
              nodePro[i1].v=value
             find = true
           // console.info("find:",find)
             break
         }
      }
      if (find==false){
        nodePro.push({"k":key,"v":value,"edit":"false","show":"false"})
      }
      // console.info("synchNodeProFromKV:",nodePro)
      return nodePro
    },
    //加载xml后画节点
    loadXMLDrawNode(){
      let that = this
      let currentNodeText = "";
      // let xmlStr = this.$refs["graphXml"].innerText;
      let xmlStr = this.$refs["graphXml"].value;
      // let xmlStr =""

      // console.info("xmlStr:",xmlStr)
      let xmlDom = etlTools.createXmlDom();
      xmlDom = etlTools.parseStringToXML(xmlDom,xmlStr);
      let nodeArray = etlTools.getNodeArrayFromXML(xmlDom,"Node");
      let lineArray = etlTools.getNodeArrayFromXML(xmlDom,"Line");
      let comment = etlTools.getNodeArrayFromXML(xmlDom,"Comment");
      let connectionArray = etlTools.getNodeArrayFromXML(xmlDom,"Connection");
      let metadataArray = etlTools.getNodeArrayFromXML(xmlDom,"Metadata");
      //画节点
      for (let i1 = 0; i1 <nodeArray.length; i1++) {
        // console.info("Node:",nodeArray[i1])
        let nodePro = [];
        let modelPro = {};




        // console.info("getAttributeNames:",nodeArray[i1].getAttributeNames())
        //生成modelPro
        for (let i2 = 0; i2 <nodeArray[i1].getAttributeNames().length ; i2++) {
          let key = nodeArray[i1].getAttributeNames()[i2];
          let value = nodeArray[i1].getAttribute(key)
          // console.info(key,value)
          //为modelPro赋值
          modelPro[key] = value;
        }

        // console.info("node modelPro",modelPro)
        //生成nodePro
        if (modelPro.type===this.nodeType.DB_INPUT_TABLE){
          nodePro = this.dbInputTable
          currentNodeText = '读数据表'
        }else if (modelPro.type===this.nodeType.DB_OUTPUT_TABLE){
          nodePro = this.dbOutputTable
          currentNodeText = '写数据表'
        }else if (modelPro.type===this.nodeType.XLS_READER){
          nodePro = this.xlsReader
          currentNodeText = '读EXCEL'
        }else if (modelPro.type===this.nodeType.XLS_WRITER){
          nodePro = this.xlsWriter
          currentNodeText = '写EXCEL'
        }else if (modelPro.type===this.nodeType.COPY_STREAM){
          nodePro = this.copyStream
          currentNodeText = '拷贝数据'
        }else if (modelPro.type===this.nodeType.TRANSFORM){
          nodePro = this.transform
          currentNodeText = '转换'
        }else if (modelPro.type===this.nodeType.DB_EXECUTE_TABLE){
          nodePro = this.dbExcuteTable
          currentNodeText = 'SQL脚本'
        }else if (modelPro.type===this.nodeType.CSV_READER){
          nodePro = this.csvReader
          currentNodeText = '读CSV'
        }else if (modelPro.type===this.nodeType.REDIS_READER){
          nodePro = this.redisReader
          currentNodeText = '读Redis'
        }else if (modelPro.type===this.nodeType.EXECUTE_SHELL){
          nodePro = this.excuteShell
          currentNodeText = '系统脚本'
        }else if (modelPro.type===this.nodeType.CUSTOM_READER_WRITER){
          nodePro = this.customReaderWriter
          currentNodeText = '自定义'
        }else if (modelPro.type==="MQ_CONSUMER" && modelPro.flag=== "ROCKETMQ"){
          nodePro = this.rocketMqConsumer
          currentNodeText = 'RocketMQ'
        }else if (modelPro.type==="MQ_CONSUMER" && modelPro.flag=== "KAFKA"){
          nodePro = this.kafkaConsumer
          currentNodeText = 'Kafka'
        }else if (modelPro.type===this.nodeType.OUTPUT_TRASH){
          nodePro = this.outputTrash
          currentNodeText = '垃圾桶'
        }else if (modelPro.type=== this.nodeType.REDIS_WRITER){
          nodePro = this.redisWriter
          currentNodeText = '写Redis'
        }else if (modelPro.type=== "MQ_PRODUCER"  && modelPro.flag=== "ROCKETMQ"){
          nodePro = this.rocketMqProducer
          currentNodeText = 'RocketMQ'
        }else if (modelPro.type=== "MQ_PRODUCER"  && modelPro.flag=== "KAFKA"){
          nodePro = this.kafkaProducer
          currentNodeText = 'Kafka'
        }
        //设计区内需要显示
        modelPro['text'] = currentNodeText
        // modelPro['isLink'] = false
        //为nodePro赋值
        for (let i3 = 0; i3 <nodePro.length ; i3++) {
          for (let k of Object.keys(modelPro)) {
            if (nodePro[i3].k===k){
              nodePro[i3].v = modelPro[k]
              break
            }
          }
        }
        //处理扩展CDATA属性 start
        let script = nodeArray[i1].getElementsByTagName("Script");
        // console.info("Script:",script[0])
        // console.info("Script childNodes:",script[0].childNodes[0])
        // console.info("Script childNodes wholeText:",script[0].childNodes[0].wholeText)
        if (script.length>0){
          let cdata = script[0].childNodes[0].wholeText.trim()
          for (let i3 = 0; i3 <nodePro.length ; i3++) {
            if (nodePro[i3].k==="script"){
              nodePro[i3].v = cdata
              break;
            }
          }
        }

        let beforeout = nodeArray[i1].getElementsByTagName("BeforeOut");
        if (beforeout.length>0){
          let cdata = beforeout[0].childNodes[0].wholeText.trim()
          for (let i3 = 0; i3 <nodePro.length ; i3++) {
            if (nodePro[i3].k==="beforeout"){
              nodePro[i3].v = cdata
              break;
            }
          }
        }

        let afterout = nodeArray[i1].getElementsByTagName("AfterOut");
        if (afterout.length>0){
          let cdata = afterout[0].childNodes[0].wholeText.trim()
          for (let i3 = 0; i3 <nodePro.length ; i3++) {
            if (nodePro[i3].k==="afterout"){
              nodePro[i3].v = cdata
              break;
            }
          }
        }
      //处理扩展CDATA属性 end


        // console.info("node nodePro",nodePro)
        //画节点
        let container = that.createContainer(parseInt(modelPro.x),parseInt(modelPro.y),
          this.NODE_PROPERTIES.width, this.NODE_PROPERTIES.height,
          currentNodeText,nodePro,modelPro)


        that.globalNodeArray.push(container)//将节点增加到全局数组中
        // console.info("加载XML添加后that.globalNodeArray：",that.globalNodeArray)
      }
      //画线
      for (let i4 = 0; i4 < lineArray.length; i4++) {
        // console.info("Line:",lineArray[i4])
        let nodePro = [];
        let modelPro = {};
        let fromNodeId = "";
        let toNodeId = "";
        let type = "";
        let fromNode = null;
        let toNode = null;
        //生成modelPro
        for (let i2 = 0; i2 <lineArray[i4].getAttributeNames().length ; i2++) {
          let key = lineArray[i4].getAttributeNames()[i2];
          let value = lineArray[i4].getAttribute(key)
          // console.info(key,value)
          //为modelPro赋值
          modelPro[key] = value;
        }
        // console.info("line modelPro:",modelPro)
        // console.info("line modelPro.length:",Object.keys(modelPro).length)
        if (Object.keys(modelPro).length>0){
          fromNodeId = modelPro['from']
          toNodeId = modelPro['to']
          type = modelPro['type']
          nodePro = [
            {"k":"from","flag":"text","v":modelPro['from'],"edit":"false","show":"true"},
            {"k":"to","flag":"text","v":modelPro['to'],"edit":"false","show":"true"},
            {"k":"type","flag":"text","v":modelPro['type'],"edit":"false","show":"true"},
            {"k":"order","flag":"text","v":modelPro['order'],"edit":"true","show":"true"},
            {"k":"metadata","flag":"metaComBox","v":modelPro['metadata'],"edit":"true","show":"true"},
          ]
          // console.info("fromNodeId_1:",fromNodeId)
          if (type==="STANDARD"){
             fromNode = that.findNodeIdForArray(fromNodeId);
             toNode = that.findNodeIdForArray(toNodeId);
          }else if (type==="COPY" || type==="TRANSFORM"){
            //COPY_STREAM_1:1 或 TRANSFORM_1:0
            let pos=fromNodeId.indexOf(":")
            if(pos!=-1){
              fromNodeId = fromNodeId.substr(0,pos)
            }
             fromNode = that.findNodeIdForArray(fromNodeId);
             toNode = that.findNodeIdForArray(toNodeId);
          }
          // console.info("fromNodeId_2:",fromNodeId)

          if(fromNode!=null && toNode!=null){
            let link = new JTopo.Link(fromNode, toNode );//曲线

            link.toArrowSize = 30;
            // link.fromArrowSize = 10;
            link.arrowsRadius = 10; //箭头大小
            link.strokeColor = '0,0,255';//线条颜色
            link.bundleOffset = 60; // 折线拐角处的长度
            link.bundleGap = 20; // 线条之间的间隔
            link.zIndex = 0//让连接线显示在节点的后面
            link.nodePro = nodePro;
            link.modelPro = modelPro;
            that.canvasPro.publicScene.add(link)
            that.globalLineArray.push(link)
            // console.info("loadXMLDrawNode::that.globalLineArray:",that.globalLineArray)
            link.addEventListener('click',function(event){
              // console.info("event.target.nodePro:",event.target.nodePro)
              // console.info("event.target.modelPro:",event.target.modelPro)
              //table编辑内容
              that.nodeTableData = event.target.nodePro

            });
          }


        }





      }
      //解析Comment 输出that.globalLineCopyStreamArray
      //[{"id":"COPY_STREAM_1:1","sortId":1},{"id":"COPY_STREAM_1:2","sortId":2}]
      if(comment.length>0) {
        // console.info("Comment:", comment[0])
        let cdata = comment[0].childNodes[0].wholeText.trim()
        let lineCopyStreamArray = JSON.parse(cdata)
        for (let i1 = 0; i1 <lineCopyStreamArray.length ; i1++) {
          that.globalLineCopyStreamArray.push(lineCopyStreamArray[i1])
        }
      }

      //解析Connection ，that.globalDbConnectArray
      for (let i1 = 0; i1 <connectionArray.length; i1++) {
        // console.info("Connection:", connectionArray[i1])
        let tmpObj = {};
        for (let i2 = 0; i2 <connectionArray[i1].getAttributeNames().length ; i2++) {
          let key = connectionArray[i1].getAttributeNames()[i2];
          let value = connectionArray[i1].getAttribute(key)
          // console.info(key,value)
          tmpObj[key] = value;
        }
        that.globalDbConnectArray.push(tmpObj)
      }
      //解析Metadata,that.globalMetadataArray
      for (let i1 = 0; i1 <metadataArray.length; i1++) {
        // console.info("Metadata:", metadataArray[i1])
        let fieldsNode = metadataArray[i1].getElementsByTagName("Field");
        let fields = []
        for (let i3 = 0; i3 < fieldsNode.length; i3++) {
          let field = {}
          for (let i5 = 0; i5 <fieldsNode[i3].getAttributeNames().length ; i5++) {
            let key = fieldsNode[i3].getAttributeNames()[i5];
            let value = fieldsNode[i3].getAttribute(key)
            field[key] = value;
          }
          fields.push(field);

        }
        let tmpObj2 = {};
        for (let i2 = 0; i2 <metadataArray[i1].getAttributeNames().length ; i2++) {
          let key = metadataArray[i1].getAttributeNames()[i2];
          let value = metadataArray[i1].getAttribute(key)
          // console.info(key,value)
          tmpObj2[key] = value;


        }
        tmpObj2['fields'] = fields;
        that.globalMetadataArray.push(tmpObj2)
      }
    }
  },

