        {"k":"fetchSize","flag":"text","v":"100","edit":"true","show":"true"},
      ],
      customReaderWriter:[
        {"k":"id","flag":"text","v":"","edit":"false","show":"true"},
        {"k":"type","flag":"text","v":"CUSTOM_READER_WRITER","edit":"false","show":"true"},
        {"k":"afterout","flag":"button","v":"","edit":"true","show":"true"},
      ],
      redisReader:[
        {"k":"id","flag":"text","v":"","edit":"false","show":"true"},
        {"k":"type","flag":"text","v":"REDIS_READER","edit":"false","show":"true"},
        {"k":"nameServer","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"password","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"db","flag":"text","v":"0","edit":"true","show":"true"},
        {"k":"isGetTTL","flag":"text","v":"false","edit":"true","show":"true"},
        {"k":"keys","flag":"button","v":"","edit":"true","show":"true"},
      ],
      redisWriter:[
        {"k":"id","flag":"text","v":"","edit":"false","show":"true"},
        {"k":"type","flag":"text","v":"REDIS_WRITER","edit":"false","show":"true"},
        {"k":"nameServer","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"password","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"db","flag":"text","v":"0","edit":"true","show":"true"},
        {"k":"isGetTTL","flag":"text","v":"false","edit":"true","show":"true"},
        {"k":"outputFields","flag":"button","v":"","edit":"true","show":"true"},
        {"k":"renameOutputFields","flag":"button","v":"","edit":"true","show":"true"},
        {"k":"beforeout","flag":"button","v":"","edit":"true","show":"true"}
      ],
      //MQ_CONSUMER for rocketmq
      rocketMqConsumer:[
        {"k":"id","flag":"text","v":"","edit":"false","show":"true"},
        {"k":"type","flag":"text","v":"MQ_CONSUMER","edit":"false","show":"true"},
        {"k":"flag","flag":"text","v":"ROCKETMQ","edit":"false","show":"true"},
        {"k":"nameServer","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"group","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"topic","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"tag","flag":"text","v":"*","edit":"true","show":"true"},
      ],
      //MQ_CONSUMER for kafka
      kafkaConsumer:[
        {"k":"id","flag":"text","v":"","edit":"false","show":"true"},
        {"k":"type","flag":"text","v":"MQ_CONSUMER","edit":"false","show":"true"},
        {"k":"flag","flag":"text","v":"KAFKA","edit":"false","show":"true"},
        {"k":"nameServer","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"group","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"topic","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"listenerFlag","flag":"text","v":"1","edit":"true","show":"true"},
      ],
    //MQ_PRODUCER for rocketmq
      rocketMqProducer:[
        {"k":"id","flag":"text","v":"","edit":"false","show":"true"},
        {"k":"type","flag":"text","v":"MQ_PRODUCER","edit":"false","show":"true"},
        {"k":"flag","flag":"text","v":"ROCKETMQ","edit":"false","show":"true"},
        {"k":"nameServer","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"group","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"topic","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"tag","flag":"text","v":"*","edit":"true","show":"true"},
        {"k":"sendFlag","flag":"text","v":"1","edit":"true","show":"true"},
        {"k":"outputFields","flag":"button","v":"","edit":"true","show":"true"},
        {"k":"renameOutputFields","flag":"button","v":"","edit":"true","show":"true"},
        {"k":"beforeout","flag":"button","v":"","edit":"true","show":"true"}
      ],
      //MQ_PRODUCER for kafka
      kafkaProducer:[
        {"k":"id","flag":"text","v":"","edit":"false","show":"true"},
        {"k":"type","flag":"text","v":"MQ_PRODUCER","edit":"false","show":"true"},
        {"k":"flag","flag":"text","v":"KAFKA","edit":"false","show":"true"},
        {"k":"nameServer","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"topic","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"isPartition","flag":"text","v":"","edit":"true","show":"true"},
        {"k":"sendFlag","flag":"text","v":"1","edit":"true","show":"true"},
        {"k":"outputFields","flag":"button","v":"","edit":"true","show":"true"},
        {"k":"renameOutputFields","flag":"button","v":"","edit":"true","show":"true"},
        {"k":"beforeout","flag":"button","v":"","edit":"true","show":"true"}
      ],


      borderButton:{
        showHidden: true,
        icon1: "el-icon-caret-left",//"el-icon-d-arrow-left",
      },
      //画布属性
      canvasPro:{
        eagleEyeEnable:false,
        publicScene:null,
        publicStage :null,
        sceneModel : "select",//"normal",//"edit"，“select”，“nodrag”
        currentNodeType:null,//当前节点类型
        currentNodeText:null,//当前节点文本
        currentNodePro:null,//属性，xml标签中的属性，是一个json数组，内容是将nodeModelPro行列转置了
        currentNodeModelPro:null,//模型,记录x,y,type,text,id,sortId,isLink 是否画线,是一个json对象
        currentContainer:null,//当前节点jtopo对象
        currentMouseX:null,
        currentMouseY:null,
        currentNodeObj:null,//当前点击的节点
      },
      // nodeArray:[],//用于排序：nodeArray.sort((a,b)=>b-a) 降序；nodeArray.sort((a,b)=>a-b)升序

      //画线逻辑
      paintingLinkPro:{
        mouseDragX:null,//鼠标拖动时x坐标
        mouseDragY:null,//鼠标拖动时y坐标
        sourceNode:null,//开始节点

        tmpBeginNode:null,//临时节点
        tmpLinkNode:null,//临时节点
      },
      //全局存放节点的数组
      globalNodeArray:[],
      //全局存放线的数组
      globalLineArray:[],
      /*
       *存储copystream连接线的id
       * [
       *  {"sortId":"1","id":"COPYSTREAM_1:1"},
       *  {"sortId":"2","id":"COPYSTREAM_1:2"},
       *  {"sortId":"3","id":"COPYSTREAM_1:3"},
       *  {"sortId":"1","id":"COPYSTREAM_2:1"},
       *  {"sortId":"2","id":"COPYSTREAM_2:2"},
       *  {"sortId":"1","id":"COPYSTREAM_3:1"}
       *  ]
      */
      globalLineCopyStreamArray:[],
      /**
       * 全局存放元数据的数组
       * [
       * {"id":"metadata_1","sortId":1,"fields":[
       *   {"name":"UUID1","type":"string","default":"-1","nullable":"false"},
       *   {"name":"UUID2","type":"string","default":"-1","nullable":"false"},
       * ]},
       * {"id":"metadata_2","sortId":2,"fields":[
       *   {"name":"UUID1","type":"string","default":"-1","nullable":"false"},
       *   {"name":"UUID2","type":"string","default":"-1","nullable":"false"},
       * ]},
       * ]
       *
       * */
      globalMetadataArray:[],
      // globalMetadataArray:[
      //    {"id":"metadata_1","sortId":1,"fields":[
      //          {"name":"UUID1","type":"string","default":"-1","nullable":"true"},
      //        {"name":"UUID2","type":"int","default":"-1","nullable":"false"},
      //      ]},
      //  {"id":"metadata_2","sortId":2,"fields":[
      //      {"name":"UUID3","type":"datetime","default":"-1","nullable":"true"},
      //      {"name":"UUID4","type":"string","default":"-1","nullable":"false"},
      //    ]},
      //  ],
      //全局存放连接的数组
      globalDbConnectArray:[],
    }


  },

}
</script>

<style scoped>
.new-collapse-item /deep/  .el-collapse-item__header{
     font-size: 15px ;
  /*padding-left:10px;*/
     display: flex;
  /*flex-direction: row-reverse;*/
     justify-content:space-between;

   margin-left: auto;
  padding-left: 10px;


  }
.new-scroll1 /deep/ .el-aside> .el-scrollbar> .el-scrollbar__wrap {
  overflow-x: hidden !important;
  /*overflow-y: hidden !important;*/
}

.contextmenu   {
  margin: 0;
  background: #fff;
  z-index: 3000;
  position: fixed; /*关键样式设置固定定位*/
  list-style-type: none;
  padding: 5px 0;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 400;
  color: #333;
  box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.3);
}
.contextmenu ul {
  margin: 0;
  padding: 7px 16px;
  cursor: pointer;
}
.contextmenu ul:hover {
  background: #eee;
}
.new-tabs-content /deep/ .el-tabs__content{
  padding: 0px!important;
}




.animate1{
  animation: shineSquare1 8s linear infinite;
}
.animate2{
  animation: shineSquare2 8s linear infinite;
}
.animate3{
  animation: shineSquare3 8s linear infinite;
}
@keyframes shineSquare1 {
  from{background: #5acb09
  }
  20%{background: #269612
  }
  40%{background: #2a6600
  }
  60%{background: #77d723
  }
  80%{background: #6e9b04
  }
  to{background: #536600
  }
}
@keyframes shineSquare2 {
  from{background: #b736db
  }
  20%{background: #972fd9
  }
  40%{background: #b85cea
  }
  60%{background: #bd9ce2
  }
  80%{background: #dab0e8
  }
  to{background: #ad2af5
  }
}
@keyframes shineSquare3 {
  from{background: #dcb113
  }
  20%{background: #cb7d09
  }
  40%{background: #663800
  }
  60%{background: #9b6904
  }
  80%{background: #af814f
  }
  to{background: #662400
  }
}
</style>



