<template>
<div style="height:100%;">


    <el-container style="height:100%;border:1px solid #eee;"   >

            <div class="new-scroll1" style="height: 100%;_background: #eef1f6;" >
              <el-aside   :style="{'width':logoWidth,'height':'100%'}" >
                <el-scrollbar  style="height: 100%; ">
                  <el-header  class="header2" :style="{'padding-left':'0px','background': this.headBackground}">
                    <el-row   type="flex" justify="space-around" align="middle" :span="24" style="margin-top:0px;width:100%;">
                      <el-col :md="24"  style="text-align: left;margin-left:5px;margin-top:0px;">
                        <span v-show="logoWidth==='200px'?true:false"   style="color:#ffffff;white-space: nowrap;margin-left:10px;" >{{this.systemDic.logoName===undefined?'管理系统':this.systemDic.logoName}}</span>
<!--                        <el-avatar  fit="contain">智慧供热</el-avatar>-->
                        <el-avatar v-show="logoWidth==='60px'?true:false"    :src="this.customGlobal.systemDictionary.logo_img===undefined?this.systemDic.defaultImg:this.systemDic.logoImg" fit="fill" style="width:40px;height:40px;text-align: left;margin-left:5px;margin-top:10px;"></el-avatar>
<!--                        <el-avatar v-show="logoWidth==='200px'?true:false"  shape="square" src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg" fit="fill" style="width:150px;height:40px;text-align: left;margin-left:5px;margin-top:10px;"></el-avatar>-->
<!--                        <el-image src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg" fit="fill" style="width:40px;height:40px;text-align: left;margin-left:5px;margin-top:10px;">-->
<!--                        </el-image>-->
                      </el-col>
                    </el-row>
                  </el-header>
                  <index-left   ref="left" @parent_method="rightAddTab" ></index-left>
                </el-scrollbar>

              </el-aside>
            </div>


              <el-container  style="height:100%;margin:0px;">
                <el-header  class="header1" :style="{'padding-left':'0px','background': headBackground}">
                  <el-row type="flex" justify="space-between" align="middle" :span="24" style="margin-top:0px;height: 60px;">
                    <el-col :md="3" style="text-align: left">
        <!--              <span style="color:#ffffff;font-size: 20px;" :class="icon1"  @click="openCloseMenu"> </span>-->
                      <el-link  style="color:#ffffff;font-size: 20px;height: 60px;line-height:60px;" :icon="icon1" :underline="false"  @click="openCloseMenu"></el-link>
                    </el-col>


                    <el-col v-show="false"  :md="16">
                      <index-left2 v-show="true"  ref="left_2" @parent_method="rightAddTab" ></index-left2>
                    </el-col>

                    <el-col :md="5" style="margin-right:30px;">
                      <div style="display: flex;justify-content: flex-end;">
                        <span style="color:#ffffff"  >{{this.customGlobal.userInfoExt.cui_realname}}&#12288;&#12288;</span>
                        <el-dropdown @command="dropdownCommand" trigger='click' >
                            <span class="el-dropdown-link  fa fa-tasks" style="font-size: 20px;">
        <!--                       <i class="el-icon-arrow-down el-icon&#45;&#45;right"></i>-->
                            </span>
                          <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item icon="el-icon-key"  command="resetPassword">修改密码</el-dropdown-item>

<!--                            <el-dropdown-item>-->
<!--                              <el-dropdown placement='left-start' @command="dropdownCommand"   >-->
<!--                                      <span class="el-dropdown-link el-icon-back" :style="{'color':this.dropDownColor ,'font-size': '14px'}">-->
<!--                                        更换系统皮肤-->
<!--                                      </span>-->
<!--                                <el-dropdown-menu slot="dropdown">-->
<!--                                  <el-dropdown-item icon="el-icon-s-marketing"  command="changeskin1">深蓝色</el-dropdown-item>-->
<!--                                  <el-dropdown-item  command="changeskin2" icon="el-icon-set-up">-->
<!--                                    <span _class="fa fa-yen-sign" :style="{'margin-left':'5px','color':this.dropDownColor ,'font-size': '14px'}"  >默认色</span>-->
<!--                                  </el-dropdown-item>-->
<!--                                </el-dropdown-menu>-->
<!--                              </el-dropdown>-->
<!--                            </el-dropdown-item>-->



<!--                            <el-dropdown-item  icon="iconfont iconico"  command="bindwx">绑定微信</el-dropdown-item>-->


                            <el-dropdown-item icon="el-icon-s-home" command="exit">退出</el-dropdown-item>

                          </el-dropdown-menu>
                        </el-dropdown>
                      </div>


                    </el-col>

                  </el-row>

                </el-header>


                    <el-container  style="height:100%;margin:0px;">
                      <el-aside style="width: 1px;background: #E3E5EB">
                      </el-aside>
                      <el-main style="padding:0px;">
                        <index-right   ref="right" @parent_method2="leftActiveMenu" ></index-right>
                      </el-main>


                    </el-container>

              </el-container>
      </el-container>

  <el-dialog :visible.sync="dialogFormVisible2"
             :close-on-press-escape="false"
             :close-on-click-modal="false"
             :show-close="false"

  >
    <div slot="title"  style="text-align: left;font-size:20px;font-weight: bold;">
      <span  >修改登录密码</span>
    </div>
    <el-form label-position="left" :model="changePassForm1" :rules="changePassRules1" ref="changePassForm1" label-width="130px"   style="text-align: left;font-size:20px;font-weight: bold;">
      <el-form-item  label="输入新密码" prop="password"  :rules="changePassRules1.password" >
        <el-input type="password" v-model="changePassForm1.password"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
              <el-button @click="resetForm('changePassForm1')">重 置</el-button>
              <el-button @click="closeForm">取 消</el-button>
              <el-button type="primary" @click="resetPassword('changePassForm1')">确 定</el-button>
    </div>
  </el-dialog>


  <el-dialog :visible.sync="dialogFormVisible3"
             :close-on-press-escape="false"
             :close-on-click-modal="false"
             :show-close="true"
             width="25%"

  >
    <div style="width:100%;height: 350px;text-align: center;">
      <wx-get-code  :key="refreshImg"></wx-get-code>
    </div>

  </el-dialog>

</div>
</template>

<script>
import IndexLeft from "../components/index/IndexLeft";
import IndexLeft2 from "../components/index/IndexLeft2";
import IndexRight from "../components/index/IndexRight";
import WxGetCode from "../components/index/right/WxGetCode";
import Bus from "../utils/Bus";
export default {
  name: "index",
  // components:{IndexRight, IndexLeft,IndexLeft2},
  components: {WxGetCode, IndexRight, IndexLeft,IndexLeft2},
  // inject:['reload'],
  data(){
    let validatePassword = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('新密码不可为空'));
      } else {
        callback();
      }
    };
    return {
      // winClientHeight :(document.documentElement.clientHeight),
      refreshImg:new Date().getTime(),
      systemDic:{
        defaultImg : require("@/assets/images/defaultLogo.jpg"),
        logoImg : this.customGlobal.serverUrl.orgBaseURI +"/fr/imgserver/"+ this.customGlobal.systemDictionary.logo_img,
        logoName: this.customGlobal.systemDictionary.company_name,
      },
      windowWidth: document.documentElement.clientWidth ,  //实时屏幕宽度
      windowHeight: document.documentElement.clientHeight ,   //实时屏幕高度

      icon1 :'fa fa-outdent',
      icon2:'fa fa-dedent',
      logoWidth:'200px',
      headBackground:'#0D5788',
      dropDownColor:'#000000',
      // bodyBgImage: 'url(' + require('@/assets/images/login_bg2.jpg') + ')'
      //loading:true,


      dialogFormVisible2:false,
      dialogFormVisible3:false,
      changePassForm1:{
        password:''
      },
      changePassRules1:{
        password:[
              {message:'新密码不可为空', validator: validatePassword, trigger:'blur'}
          ],
      }
    }
  },
  destroyed() {
    Bus.$off('globalMsgForTabPage');
   // window.removeEventListener('beforeunload', e => this.test(e))
  },
  created() {
    let that = this;
    Bus.$on('globalMsgForTabPage',function(val){//监听各组件传递的 globalMsg 事件
      // console.log("监听到的消息："+JSON.stringify(val));
      console.info("val:"+JSON.stringify(val));
      console.log('res==', val);
      if(val.method==='open'){
        that.$refs['right'].removeTab(val.id);
        that.$refs['right'].addTab(val);
      }else if(val.method==='close'){
        that.$refs['right'].removeTab(val.id);
      }


    });
    //this.stopF5Refresh();
    //window.addEventListener('beforeunload', e => this.test(e),false);
    // setTimeout(() => {
    //   this.loading = false;
    // }, 2000);
    // document.body.className = "custom-1";

    let theme = window.localStorage.getItem('theme');

    if(theme=='custom-0') {//默认
      this.headBackground = '#0D5788';
      this.dropDownColor = '#000000';
    }else if(theme=='custom-1'){
      this.headBackground = '';
      this.dropDownColor = '#ffffff';
    }else{

      this.headBackground = '#0D5788';
      this.dropDownColor = '#000000';
    }

    if(theme){
      document.body.className = theme;
    }

  },

  mounted() {

    // document.body.style.backgroundImage = this.bodyBgImage;
    let that = this;
    // <!--把window.onresize事件挂在到mounted函数上-->

    window.addEventListener("resize",   function(){
      window.fullHeight = document.documentElement.clientHeight;
      window.fullWidth = document.documentElement.clientWidth;
      that.windowHeight = window.fullHeight+'px';  // 高
      that.windowWidth = window.fullWidth; // 宽
      that.customGlobal.win_w_h = {width:that.windowWidth,height:that.windowHeight};
      console.info("win_w_h:"+JSON.stringify( that.customGlobal.win_w_h));
    });
    //刷新后重新加载
    let currentTab = window.sessionStorage.getItem('currentTab');
    if(currentTab){
      // this.rightAddTab(JSON.parse(currentTab));
      // this.$refs['right'].addTab(JSON.parse(currentTab));

      this.$refs['right'].addTab(JSON.parse(this.customGlobal.decrypt(currentTab,this.customGlobal.cryptoKey)));
    }
  },
  methods:{
    leftActiveMenu(args){
      this.$refs['left'].activeMenu(args);
    },
    rightAddTab(args){//一个参数

        //alert("父组件接受子组件的内容："+JSON.stringify(args));
        this.$refs['right'].addTab(args);
        //alert("父组件接受子组件的内容："+ args);
    },
    openCloseMenu(){
        console.info("menuCollapse:"+this.$refs['left'].menuCollapse);
        this.$refs['left'].openCloseMenu();;

        if(this.$refs['left'].menuCollapse){
          this.icon1 = "fa fa-indent";
          this.logoWidth = '60px';
        }else{
          this.icon1 = "fa fa-outdent";
          this.logoWidth = '200px';

        }
    },
    closeForm(){
      this.dialogFormVisible2 = false;
      this.$refs.changePassForm1.clearValidate();
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();

    },

    resetPassword(formName){
      console.info("窗体元素："+JSON.stringify( this.changePassForm1) );
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let md5password = this.customGlobal.md5(this.changePassForm1.password);
          const loading = this.$loading({
            lock: true,
            text: '提交中...',
            target: '.main',
            spinner: 'el-icon-loading',

            background: 'rgba(0, 0, 0, .7)'
          });

          var p1 = this.customGlobal.postHttpClient(
            this.customGlobal.serverUrl.postChangeMerPasswd2Bank,
            //this.customGlobal.serverUrl.baseURI,
            this.customGlobal.serverUrl.orgBaseURI,
            {"Content-Type": "application/x-www-form-urlencoded"},
            this.qs.stringify({jsondata:JSON.stringify({username:this.customGlobal.userInfoExt.cui_login_id,password: md5password })})
          );

          p1.then((res) => {
            console.log("异步调用成功返回:"+JSON.stringify(res));
            if(res.data.status=='success'){
              console.info("修改密码成功");

              this.$message({
                type: 'success',
                showClose: true,
                duration:3000,
                dangerouslyUseHTMLString: true,
                message: '<span style="color:black;"> 修改密码成功</span>'
              });
              loading.close();

            }else{
              console.info("创建失败");

              this.$message({
                type: 'warning',
                showClose: true,
                duration:3000,
                dangerouslyUseHTMLString: true,
                message: '<span style="color:black;"> 修改密码失败</span>'
              });


              loading.close();


            }

          },error=>{
            console.log("异步调用出错:"+JSON.stringify(error));
            loading.close();
            this.$alert(JSON.stringify(error.message), '修改密码出错!', {
              confirmButtonText: '确定',
              callback: action => {

              }
            });
          }).catch((res) => {
            console.log("异步调用失败返回:"+JSON.stringify(res));
            loading.close();

            this.$alert(JSON.stringify(res.message), '修改密码出错', {
              confirmButtonText: '确定',
              callback: action => {

              }
            });
          });


          this.dialogFormVisible2 = false;
        } else {

          return false;
        }
      });
    },
    dropdownCommand(cmd){
      //this.$message('click on item ' + cmd);
      if(cmd==='changeskin2'){//默认
        document.body.className = "custom-0";
        window.localStorage.setItem('theme',"custom-0");
        this.headBackground = '#0D5788';
        this.dropDownColor = '#000000';

      }else if(cmd==='changeskin1'){//深蓝
        document.body.className = "custom-1";
        window.localStorage.setItem('theme',"custom-1");
        this.headBackground = '';
        this.dropDownColor = '#ffffff';


      }else if(cmd==='exit'){
        var p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postMerLoginSessionClear,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/json"},
          {});
        this.customGlobal.userInfo =  {username:"null",authed:"failed"};
        window.sessionStorage.clear();
        this.$router.push({path:'/login'});

      }else if(cmd==='resetPassword') {
        this.changePassForm1.password = '';
        this.dialogFormVisible2 = true;
      }else if(cmd==='bindwx'){
        this.refreshImg = new Date().getTime();
        this.dialogFormVisible3 = true;
      }
    },

  },

}
</script>

<style scoped>
 /*<style >*/


.header1{
  padding:0px;height: 60px;line-height:60px;text-align: left; font-size: 14px;
}
.header2{
  padding:0px;width:100%;height: 60px;line-height:60px;text-align: left; font-size: 14px;
}

/*隐藏滚动条*/
.is-horizontal {
  display: none;
}
.el-dropdown-link {
  cursor: pointer;
  /*color: #409EFF;*/
  color: #FFFF;
}
.el-icon-arrow-down {
  font-size: 12px;
}

.el-link{
  text-decoration: none!important;
}

 .new-scroll1 /deep/ .el-aside> .el-scrollbar> .el-scrollbar__wrap {
   overflow-x: hidden !important;
   /*overflow-y: hidden !important;*/
 }
/*.new-scroll1 /deep/ .el-aside>.el-scrollbar>.el-scrollbar__wrap {*/
/*  overflow-x: hidden !important;*/
/*  overflow-y: hidden !important;*/
/*}*/
</style>



